
#liu-audiometry {
  width: 1330px;
  height: 600px;
  margin: 80px auto;
}
#liu-audiometry .audiometry-right {
  width: 420px;
  background-color: #fff;
  height: 100%;
  border: 6px solid #5e9b71;
}
#liu-audiometry .audiometry-right .active {
  background-color: #5c946c;
  color: #fff;
}
#liu-audiometry .audiometry-right .title {
  border-bottom: 3px solid #5e9b71;
}
#liu-audiometry .audiometry-right .title h3 {
  margin: 0px;
  margin-left: 30px;
  line-height: 50px;
  font-size: 20px;
}
#liu-audiometry .audiometry-right dt,
#liu-audiometry .audiometry-right h3,
#liu-audiometry .audiometry-right .demonstration {
  color: #579b6a;
}
#liu-audiometry .audiometry-right .audiometry-main dl {
  margin-top: 30px;
}
#liu-audiometry .audiometry-right .audiometry-main dt,
#liu-audiometry .audiometry-right .audiometry-main .demonstration {
  float: left;
  margin-left: 65px;
  margin-top: 4px;
  font-size: 22px;
}
#liu-audiometry .audiometry-right .audiometry-main dd {
  float: left;
}
#liu-audiometry .audiometry-right .audiometry-main .demonstration {
  margin: 0 5px 0 30px;
  height: 50px;
  line-height: 50px;
}
#liu-audiometry .audiometry-right .audiometry-main .el-checkbox-button.is-disabled .el-checkbox-button__inner {
  min-width: 97px !important;
  height: 50px;
  padding: 0;
  line-height: 50px;
  font-size: 18px;
}
#liu-audiometry .audiometry-right .button {
  margin: 45px 88px;
}
#liu-audiometry .audiometry-right .button ul,
#liu-audiometry .audiometry-right .button li {
  list-style: none;
  margin: 0px;
  padding: 0px;
}
#liu-audiometry .audiometry-right .button .el-button + .el-button {
  margin-left: 0px;
}
#liu-audiometry .audiometry-right .button button {
  width: 260px;
  height: 60px;
  margin-bottom: 10px;
  border-radius: 5px;
  cursor: pointer;
  color: #fff;
  text-align: center;
  font-size: 24px;
}
#liu-audiometry .audiometry-left {
  width: 770px;
  border: 6px solid #5e9b71;
  height: 600px;
  background-color: #fff;
}
#liu-audiometry .audiometry-left .liu-top {
  font-size: 20px;
  margin: 30px 70px 15px 70px;
  color: #333;
}
#liu-audiometry .audiometry-left .liu-table {
  margin: 0px auto;
  width: 90%;
  height: 170px;
  /*表头背景色*/
  /*背景色高亮*/
}
#liu-audiometry .audiometry-left .liu-table table {
  width: 100%;
  /*height: 100%;*/
  border-collapse: collapse;
}
#liu-audiometry .audiometry-left .liu-table .headerbg {
  background-color: #eef1f6;
}
#liu-audiometry .audiometry-left .liu-table .gray {
  background-color: #eee;
}
#liu-audiometry .audiometry-left .liu-table .highlight {
  background-color: orange;
  box-shadow: 0 0 5px orange;
  opacity: 0.3;
  -webkit-animation-name: light;
  -webkit-animation-duration: 500ms;
  -webkit-animation-iteration-count: infinite;
  -webkit-animation-timing-function: ease-in-out;
}
#liu-audiometry .audiometry-left .liu-table .pasehighlight {
  background-color: orange;
  box-shadow: 0 0 5px orange;
}
@-webkit-keyframes light {
0% {
    opacity: 0.1;
}
100% {
    opacity: 1;
}
}
#liu-audiometry .audiometry-left .liu-table td,
#liu-audiometry .audiometry-left .liu-table th {
  box-sizing: border-box;
  text-align: center;
  padding: 0px;
  border: 1px solid #ccc;
  width: 50px;
}
#liu-audiometry .audiometry-left .liu-table tr {
  height: 45px;
}
#liu-audiometry .tips .el-dialog__header,
#liu-audiometry .tips .el-dialog__body {
  padding: 0px;
}
#liu-audiometry .tips img {
  width: 100%;
}



