
.drawsvg {
  height: 100%;
  width: 100%;
  /*******tab标签*******/
}
.drawsvg .l-ear,
.drawsvg .r-ear {
  height: 100%;
}
.drawsvg .clear {
  clear: both;
}
.drawsvg .ibox-title {
  margin-top: 5px;
}
.drawsvg .ibox-title label {
  border: 0px none;
}
.drawsvg .ibox-title .ibox-title-row {
  position: relative;
  cursor: pointer;
  width: 120px;
  height: 35px;
  line-height: 35px;
  text-align: center;
  border: 1px solid #ccc;
  margin-right: 10px;
}
.drawsvg .ibox-title .ibox-title-row .arrows {
  font-size: 14px;
  position: absolute;
  right: 5px;
  top: 12px;
  -webkit-transform: rotate(0);
          transform: rotate(0);
  transition: -webkit-transform 0.4s ease-in-out;
  transition: transform 0.4s ease-in-out;
  transition: transform 0.4s ease-in-out, -webkit-transform 0.4s ease-in-out;
}
.drawsvg .ibox-title .ibox-title-row .arrowsActive {
  right: 3px;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.drawsvg .ibox-title .is-checked .el-checkbox-button__inner {
  background-color: #278bc4 !important;
  border-color: #278bc4 !important;
  color: #ffffff;
  box-shadow: #278bc4 -1px 0px 0px 0px !important;
}
.drawsvg .ibox-title .el-checkbox-button__inner {
  padding: 10px 5px;
  width: 105px;
  border: 1px solid #ccc;
}



