
.infotable .title {
  height: 40px;
  background-color: #f8f8f8;
  padding: 0px 15px;
  border-bottom: 1px solid #ccc;
  line-height: 40px;
}
.infotable .title h3 {
  margin: 0px;
  color: #1f2d3d;
  font-weight: 600;
}
.infotable .title div {
  color: #bfcbda;
  width: 30px;
  text-align: center;
  cursor: pointer;
}
.infotable .title div:hover {
  color: #50bfff;
}
.infotable .infotablecontent {
  margin: 50px 20px 20px 20px;
}
.infotable .infotablecontent h3 {
  position: absolute;
  left: 30px;
  top: -90px;
  width: 300px;
  height: 90px;
  background: url(../../../../static/images/new-title.png) no-repeat 0 -185px;
}
.infotable .el-dialog--small {
  width: 900px;
  border-radius: 20px;
}
.infotable .el-dialog__body {
  padding: 0;
  position: relative;
}
.infotable .fl .el-button {
  padding: 0;
}
.infotable .search {
  margin-bottom: 8px;
}
.infotable .search .input {
  width: 280px;
  float: left;
  margin-right: 19px;
}
.infotable .search span {
  margin: 0 10px;
  float: left;
  font-size: 16px;
  font-weight: 550;
  line-height: 40px;
}
.infotable .search .el-input__inner {
  height: 43px;
}
.infotable .table {
  margin-top: 8px;
}
.infotable .table .t-s-z,
.infotable .table .t-s-l,
.infotable .table .t-s-d {
  cursor: pointer;
  width: 425px;
  height: 40px;
  border: 1px solid #ccc;
  line-height: 40px;
  text-align: center;
  background: #ccc;
  font-size: 18px;
  color: #000;
  border-radius: 7px 0 0 7px;
}
.infotable .table .t-s-z p,
.infotable .table .t-s-l p,
.infotable .table .t-s-d p {
  letter-spacing: 10px;
}
.infotable .table .t-s-z .el-badge,
.infotable .table .t-s-l .el-badge,
.infotable .table .t-s-d .el-badge {
  width: 100%;
}
.infotable .table .t-s-d {
  border-left: 0px none;
  border-left: 1px solid #fff;
  border-radius: 0 7px 7px 0;
}
.infotable .table .active {
  background-color: #109bff;
  border: 1px solid #109bff;
  border-left: 1px solid #fff;
  color: #fff;
}
.infotable .table .t-6 .t-s-l,
.infotable .table .t-6 .t-s-d,
.infotable .table .t-6 .t-s-z {
  width: 283px;
}
.infotable .table .t-6 .t-s-z {
  border-radius: 0;
  border-left: 1px solid #fff;
}
.infotable .table ul {
  height: 320px;
  overflow: auto;
}
.infotable .table ul p {
  font-size: 22px;
  text-align: center;
}
.infotable .table li {
  width: 110px;
  box-sizing: border-box;
  cursor: pointer;
  height: 140px;
  font-size: 20px;
  font-weight: 500;
  letter-spacing: 3px;
  border-radius: 5px;
  border: 2px solid #449d44;
  box-shadow: 4px 4px 10px  #999;
  color: #000;
  margin: 10px 15px;
}
.infotable .table li img {
  width: 90px;
  height: 90px;
  border-radius: 50%;
  display: block;
  margin: 5px auto;
}
.infotable .table li span {
  box-sizing: border-box;
  overflow: hidden;
  white-space: nowrap;
  display: block;
  padding: 0 10px;
  text-align: center;
}
.infotable .table .no-test {
  border: 2px solid red;
}
.infotable .el-table_1_column_5 {
  overflow: hidden;
  /*自动隐藏文字*/
  text-overflow: ellipsis;
  /*文字隐藏后添加省略号*/
  white-space: nowrap;
  /*强制不换行*/
}
.infotable .last {
  margin-left: 20px;
}
.infotable .last li {
  text-align: center;
  list-style: none;
  float: left;
  cursor: pointer;
  margin: 3px 3px;
  color: #20a0ff;
  background-color: #e9f6ff;
  border-radius: 5px;
  padding: 0px 8px;
  line-height: 35px;
  border: 1px solid #c0e5ff;
  font-size: 16px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  box-shadow: 2px 2px 5px #bbb;
}
.infotable .last .l-t {
  font-size: 16px;
  width: 80px;
  cursor: auto;
  border: 1px solid #eee;
  background-color: #eee;
  color: #333;
  box-shadow: 0px 0px 0px red;
}
.infotable .last .sef {
  background-color: #fef8ea;
  border: 1px solid #fcebc3;
  color: #f7bd36;
}
div.el-message {
  top: 50%;
  min-width: 150px;
  background-color: rgba(0, 0, 0, 0.3);
}
div.el-message p {
  color: #fff;
}



