
.add-name {
  /*border: 3px solid #109bff;*/
  box-shadow: 0 0 30px #000;
  border-radius: 15px !important;
}
.add-name .el-message-box__header {
  display: none;
}
.s-b-g .el-dialog {
  width: 800px;
}
.s-b-g .inner .dele .el-button {
  font-size: 18px;
}
.s-b-g .inner ul li {
  float: left;
  width: 180px;
  margin: 0 5px;
  padding: 10px;
  color: #000;
}
.s-b-g .inner ul li .el-input {
  margin-bottom: 5px;
}
.s-b-g .inner ul li:first-child {
  width: 100px;
}
.s-b-g .inner ul dt {
  font-size: 20px;
  text-align: center;
  /*font-weight: 600;*/
  height: 40px;
  line-height: 40px;
  margin-bottom: 5px;
}
.s-b-g .inner ul .title {
  background-color: #13ce66;
  border-radius: 3px;
  color: #fff;
  cursor: pointer;
  box-shadow: 3px 3px 2px rgba(0, 0, 0, 0.2);
}
.s-b-g .inner ul dl > div {
  height: 250px;
  border: 2px solid #ccc;
  overflow: auto;
  border-radius: 5px;
  padding-top: 5px;
}
.s-b-g .inner ul dd {
  margin: 0;
  box-sizing: border-box;
  cursor: pointer;
  width: 100%;
  height: 40px;
  padding: 0 5px;
  font-size: 18px;
  line-height: 40px;
  margin: 5px 0;
  position: relative;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.s-b-g .inner ul dd i {
  position: absolute;
  right: 0;
  top: 0;
  color: red;
  width: 40px;
  height: 40px;
  text-align: center;
  line-height: 40px;
}
.s-b-g .inner ul dd .showDele {
  display: none;
}
.s-b-g .inner ul .active {
  background-color: #209ffe;
  box-shadow: 2px 2px 1px #ccc;
  color: #fff;
}



