
.base {
  /*地址*/
  /*表单验证*/
  /*输入框样式修改*/
}
.base dt {
  float: left;
  width: 80px;
  text-align: right;
  font-weight: 600;
  font-size: 14px;
}
.base .upLoad,
.base .wszl {
  cursor: pointer;
}
.base .address select {
  width: 109px;
  padding: 0;
  padding-left: 5px;
  border-radius: 0;
  height: 35px;
  margin-top: 5px;
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
}
.base .print-tone {
  border-bottom: 1px solid #ccc;
}
.base .studyBase {
  position: relative;
}
.base .studyBase img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  position: relative;
  left: 50%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  top: 30px;
}
.base .studyBase .studyBase-middle {
  width: 200px;
  position: relative;
  left: 50%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  margin-top: 50px;
  height: 50px;
}
.base .studyBase .studyBase-middle .name {
  /*float: left;*/
  position: absolute;
  left: 50%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  font-size: 26px;
  font-weight: 100;
  margin-left: -35px;
  width: 150px;
  text-align: center;
  overflow: hidden;
  /*自动隐藏文字*/
  text-overflow: ellipsis;
  /*文字隐藏后添加省略号*/
  white-space: nowrap;
  /*强制不换行*/
}
.base .studyBase .studyBase-middle .update {
  margin: 0px;
}
.base .studyBase .studyBase-middle .upLoad {
  position: absolute;
  left: 50%;
  -webkit-transform: translate(50%);
          transform: translate(50%);
  /*float: left;*/
  background-color: #5cb85c;
  color: #fff;
  width: 70px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  border-radius: 5px;
  /*margin-left: 20px;*/
  margin-top: 3px;
  font-weight: 200;
  font-size: 14px;
}
.base .studyBase p {
  text-align: center;
  border: 1px solid #ccc;
  margin: 0 auto;
  width: 215px;
  border-radius: 7px;
  line-height: 30px;
  color: #333;
  cursor: pointer;
}
.base .studyBase .studyBase-bottom {
  position: absolute;
  left: 50%;
  margin-top: 30px;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  z-index: 100;
}
.base .studyBase .studyBase-bottom .wszl {
  width: 90px;
  height: 35px;
  font-size: 16px;
  color: #fff;
  background-color: #1ab394;
  border-radius: 5px;
  line-height: 35px;
  text-align: center;
  /*float: left;*/
  margin-right: 5px;
  margin-top: 20px;
  padding: 0 5px;
}
.base .studyBase .studyBase-bottom .wszl i {
  margin: 0 5px;
}
.base .studyBase .studyBase-bottom .dele {
  background-color: #d9534f;
}
.base .studyBase .studyBase-bottom .bc {
  /*float: left;*/
}
.base .studyBase .studyBase-bottom .bc div {
  color: #fff;
  font-weight: 200;
  font-size: 16px;
  width: 65px;
  height: 35px;
  border-radius: 5px;
  line-height: 35px;
  text-align: center;
  float: left;
}
.base .studyBase .studyBase-bottom .bc div:first-child {
  background-color: #1ab394;
  margin-right: 10px;
  cursor: pointer;
}
.base .studyBase .studyBase-bottom .bc div:last-child {
  background-color: #d9534f;
  cursor: pointer;
}
.base .base-form .el-form-item {
  float: left;
  margin-bottom: 9px;
}
.base .base-form .el-form-item__error {
  padding-top: 0px;
}
.base dl,
.base .base-form {
  /*padding:10px;*/
  margin: 0px;
  font-size: 14px;
  /*选择框图标样式*/
  /*性别样式修改*/
}
.base dl dt,
.base .base-form dt {
  margin: 10px 5px 0 0;
}
.base dl input,
.base .base-form input {
  width: 200px;
  height: 35px;
  margin-top: 5px;
  border: 1px solid #ccc;
  padding: 0px 10px;
}
.base dl .el-input__inner:focus,
.base .base-form .el-input__inner:focus {
  border: 1px solid #8daee5;
}
.base dl .el-input__inner,
.base .base-form .el-input__inner {
  width: 223px;
  border-radius: 0px;
}
.base dl .el-input__icon,
.base .base-form .el-input__icon {
  right: -25px;
}
.base dl .sex input,
.base .base-form .sex input {
  width: 15px;
  height: 15px;
  margin-top: 15px;
}
.base .el-select .el-input .el-input__icon {
  right: 0px;
}
.base .textarea {
  max-width: 320px;
  margin-top: 10px;
}
.base .base-bottom .base-bottom-main {
  /*margin: 0 auto;*/
  max-width: 900px;
  max-height: 300px;
}
.base .base-bottom .base-bottom-warp {
  border: 1px solid #ccc;
  margin: 20px 15px;
  border-radius: 5px;
  height: 220px;
}
.base .base-bottom .base-bottom-warp dl {
  padding: 10px;
  margin: 0px;
}
.base .base-bottom .base-bottom-warp dl input {
  width: 200px;
  height: 35px;
  margin-top: 5px;
  border: 1px solid #ccc;
  padding: 0px 10px;
}
.base .base-bottom .base-bottom-warp dl .el-input__inner:focus {
  border: 1px solid #8daee5;
}
.base .base-bottom .base-bottom-warp dl .el-input__inner {
  width: 223px;
  border-radius: 0px;
}
.base .base-bottom .base-bottom-warp .check-class .el-input {
  width: auto;
}
.base .base-bottom h3 {
  margin: 0px;
  background-color: #f5f5f5;
  border-color: #ddd;
  font-weight: 200;
  height: 40px;
  line-height: 40px;
  border-bottom: 1px solid #ccc;
  padding-left: 20px;
}
.check-class .el-input {
  width: auto;
}
.infoBase div dt,
.infoBase div dd {
  /*float: none;*/
  margin: 0px;
  height: 30px;
  line-height: 30px;
  padding: 3px;
}
/*媒体查询*/
@media screen and (max-width: 1480px) {
.base dl input,
  .base .base-bottom .base-bottom-warp dl input {
    width: 155px;
    height: 30px;
}
.base dl .el-input__inner,
  .base .base-bottom .base-bottom-warp dl .el-input__inner {
    width: 177px;
}
.base dl .el-input__inner,
  .base .base-bottom .base-bottom-warp dl .el-input__icon {
    right: 10px;
}
.base .eq-sb input {
    height: 31px;
}
.base dl .el-input__icon {
    right: 10px;
}
}



