
.especial-params {
  width: 1150px;
  height: 700px;
  margin: 0px auto ;
  margin-top: 60px;
  background-color: #fff;
  border: 8px solid #5f9970;
}
.especial-params .title {
  border-bottom: 3px solid #5b9b69;
  margin: 0px 20px;
}
.especial-params .title h3 {
  font-weight: 500;
  margin: 0px;
  line-height: 50px;
  font-size: 25px;
}
.especial-params .title .el-button {
  line-height: 24px;
  padding: 5px 15px;
}
.especial-params .bg {
  margin-right: 6px;
  margin-top: 7px;
}
.especial-params .el-checkbox {
  width: 45px;
  text-align: center;
}
.especial-params .el-checkbox .el-checkbox__label {
  display: block;
}
.especial-params .params-main {
  padding-left: 100px;
  /*单选按钮开始*/
  /*单选按钮结束*/
}
.especial-params .params-main dl {
  margin-top: 25px;
}
.especial-params .params-main .checkradio .el-radio-button__orig-radio:checked + .el-radio-button__inner,
.especial-params .params-main .checkVoideType .el-radio-button__orig-radio:checked + .el-radio-button__inner {
  background-color: #13ce66;
  border: 1px solid #13ce66;
  box-shadow: -1px 0 0 0 #13ce66;
}
.especial-params .params-main .checkVoideType .el-radio-button__inner {
  padding: 10px 10px;
}
.especial-params .params-main .checkVoideType .el-radio-button__inner i {
  font-size: 18px;
}
.especial-params .params-main ul {
  width: 850px;
}
.especial-params .params-main ul li {
  width: 570px;
  position: relative;
  height: 35px;
  line-height: 35px;
  /*margin-right: 30px;*/
  border: 1px solid #ccc;
  border-radius: 5px;
  margin: 5px 30px 15px 0px;
}
.especial-params .params-main ul li .el-radio-group {
  margin-left: 40px;
}
.especial-params .params-main ul li .el-radio-group p {
  line-height: 35px;
  margin-right: 10px;
}
.especial-params .params-main ul li .el-radio-group p .el-radio__label {
  float: left;
  text-align: center;
  width: 60px;
  font-size: 18px;
  margin-left: 10px;
}
.especial-params .params-main ul li .check-main {
  position: absolute;
  left: 0px;
  top: 0px;
  width: 100%;
}
.especial-params .params-main ul li .check-main .item-title {
  border-radius: 5px 0 0 5px;
  background-color: #13ce66;
  width: 56px;
  color: #fff;
  font-size: 16px;
  font-weight: 600;
  text-indent: 20px;
  height: 35px;
}
.especial-params .params-main ul li .el-checkbox__label {
  visibility: hidden;
}
.especial-params .params-main dt,
.especial-params .params-main dd {
  float: left;
}
.especial-params .params-main dt {
  font-size: 18px;
  color: #3377b8;
}
.especial-params .params-main .check {
  margin-left: 115px;
  margin-top: 20px;
}



