
.liu-params {
  width: 1150px;
  height: 700px;
  margin: 0px auto ;
  margin-top: 60px;
  background-color: #fff;
  border: 8px solid #5f9970;
}
.liu-params .title {
  border-bottom: 3px solid #5b9b69;
  margin: 0px 20px;
}
.liu-params .title h3 {
  font-weight: 500;
  margin: 0px;
  line-height: 50px;
  font-size: 25px;
}
.liu-params .title span {
  line-height: 50px;
  font-size: 28px;
  color: #5f9770;
}
.liu-params .title span i {
  cursor: pointer;
}
.liu-params .el-checkbox {
  width: 45px;
  text-align: center;
}
.liu-params .el-checkbox .el-checkbox__label {
  display: block;
}
.liu-params .params-main {
  padding-left: 100px;
