  /*单选按钮结束*/
}
.liu-params .params-main dl {
  margin-top: 25px;
}
.liu-params .params-main dl .lin-tone span {
  float: left;
  margin-left: 15px;
  font-size: 26px;
  line-height: 25px;
}
.liu-params .params-main .checkradio .el-checkbox-button.is-checked .el-checkbox-button__inner,
.liu-params .params-main .checkradio .el-radio-button__orig-radio:checked + .el-radio-button__inner {
  background-color: #13ce66;
  border: 1px solid #13ce66;
  box-shadow: -1px 0 0 0 #13ce66;
}
.liu-params .params-main ul {
  width: 850px;
}
.liu-params .params-main dt,
.liu-params .params-main dd {
  float: left;
}
.liu-params .params-main dt {
  font-size: 18px;
  color: #3377b8;
}
.liu-params .params-main .check {
  margin-left: 115px;
  margin-top: 20px;
}



