
.statistic {
  height: 94%;
  width: 100%;
  background: #fff;
}
.statistic .title {
  height: 50px;
  line-height: 50px;
  border-bottom: 1px solid #88d99f;
}
.statistic .title h3 {
  font-size: 20px;
  font-weight: 600;
  text-indent: 2em;
}
.statistic section {
  max-width: 1600px;
  margin: 0 auto;
  padding-left: 50px;
}
.statistic section dt,
.statistic section dd,
.statistic section .check-all {
  float: left;
  height: 40px;
  line-height: 40px;
}
.statistic section dt {
  width: 110px;
  text-align: right;
}
.statistic section dd,
.statistic section .check-all {
  margin: 0 5px;
  width: 110px;
  background: #ddd;
  margin-bottom: 10px;
  text-align: center;
  color: #666;
  letter-spacing: 2px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 18px;
  border: 1px solid #bbb;
  box-shadow: 3px 3px 2px #ddd;
}
.statistic section .checked-obj {
  background: #20a0ff;
  border: 1px solid #20a0ff;
  background: #00ca00;
  border: 1px solid #00ca00;
  color: #fff;
}
.statistic section .checked {
  background: #20a0ff;
  border: 1px solid #20a0ff;
  color: #fff;
}
.statistic section .pie .warp-f-dd {
  width: 800px;
  height: 50px;
  overflow: hidden;
  float: left;
}
.statistic section .pie .warp-dd {
  height: 50px;
  -webkit-animation: myclose 0.5s;
          animation: myclose 0.5s;
}
.statistic section .pie .on {
  height: 105px;
  -webkit-animation: myopen 0.5s;
          animation: myopen 0.5s;
  overflow: auto;
}
@-webkit-keyframes myopen {
from {
    height: 50px;
}
to {
    height: 105px;
}
}
@keyframes myopen {
from {
    height: 50px;
}
to {
    height: 105px;
}
}
@-webkit-keyframes myclose {
from {
    height: 105px;
}
to {
    height: 50px;
}
}
@keyframes myclose {
from {
    height: 105px;
}
to {
    height: 50px;
}
}
.statistic section .pie a {
  float: left;
  height: 40px;
  padding-left: 10px;
  line-height: 40px;
  background-color: #ccc;
  text-decoration: none;
  color: #fff;
  border-radius: 3px;
  margin-left: 30px;
}
.statistic section .pie a i {
  height: 40px;
  padding: 0 10px;
  line-height: 40px;
  display: inline-block;
  border-left: 1px solid #666;
}
.statistic section .s-xm {
  margin-top: 50px;
}
.statistic section .s-xm div {
  width: 250px;
}
.statistic section .s-xm div .clear {
  display: block;
  clear: both;
  height: 0;
  line-height: 0;
}
.statistic section .s-xm dd {
  margin-bottom: 20px;
}
.statistic section .s-xm dd,
.statistic section .s-xm p span {
  float: left;
  height: 35px;
  line-height: 35px;
  font-size: 18px;
  background-color: #ddd;
  color: #666;
  border: 1px solid #bbb;
}
.statistic section .s-xm .checked-obj {
  background: #00ca00;
  border: 1px solid #00ca00;
  color: #fff;
}
.statistic section .s-xm dl {
  width: 420px;
}
.statistic section .s-xm p {
  width: 250px;
  border-top: 2px solid #bbb;
}
.statistic section .s-xm p span {
  cursor: pointer;
  display: block;
  margin: 15px 0;
  border-radius: 5px;
  box-shadow: 3px 3px 2px #ccc;
  float: none;
  text-align: center;
}
.statistic section .s-xm .pinggu {
  background: #800080;
  border: 1px solid #800080;
  color: #fff;
}
.statistic .ping-over li {
  height: 30px;
  line-height: 30px;
  margin: 20px 0;
}
.statistic .ping-over li .progress {
  width: 320px;
}
.statistic .ping-over li .el-progress {
  margin-top: 7px;
}
.statistic .ping-over li em {
  width: 110px;
  display: inline-block;
  text-align: center;
}
.statistic .ping-over li span,
.statistic .ping-over li .progress,
.statistic .ping-over li a {
  float: left;
}
.statistic .ping-over p > em {
  font-size: 20px;
  margin-right: 85px;
  line-height: 35px;
}
.statistic .ping-over strong {
  color: red;
  font-weight: 600;
}
.statistic .ping-over li a {
  background-color: #f7ba2a;
  letter-spacing: 2px;
}
.statistic .ping-over p span {
  background-color: #ccc;
  cursor: pointer;
}
.statistic .ping-over p span,
.statistic .ping-over li a {
  margin: 0 10px;
  padding: 0 10px;
  display: inline-block;
  border-radius: 4px;
  height: 35px;
  line-height: 35px;
  color: #fff;
  text-decoration: none;
  box-shadow: 2px 2px 2px #ccc;
}
.statistic .ping-over .p-time {
  background-color: #f7ba2a;
}
.statistic .pie-warp,
.statistic .ping-over {
  width: 630px;
  height: 400px;
  margin-left: 150px;
  margin-top: 35px;
}
@media (max-width: 1490px) {
.statistic .pie-warp {
    width: 600px;
    height: 300px;
    margin-left: 150px;
    margin-top: 0;
}
.statistic .ping-over {
    margin-top: 0;
    height: 300px;
}
}



