
.assessment {
  padding-left: 20px;
}
.assessment .assessment-title ul {
  height: 50px;
  line-height: 50px;
}
.assessment .assessment-title ul li {
  cursor: pointer;
  border-radius: 5px 0 0 5px;
  background-color: #eee;
  width: 110px;
  text-align: center;
  height: 40px;
  line-height: 40px;
  color: #337ab7;
  margin-bottom: 10px;
  box-shadow: 2px 2px 2px #ccc;
}
.assessment .assessment-title ul .typeclass {
  background-color: #65c0df;
  color: #fff;
}
.assessment .assessment-content {
  margin-top: 30px;
}
.assessment .assessment-content .assessment-test {
  border: 3px solid #65c0df;
  background: #65c0df;
  border-radius: 0 13px 13px 13px;
  box-shadow: 1px 1px 5px #65c0df;
  min-width: 200px;
  max-width: 300px;
  height: 500px;
}
.assessment .assessment-content .assessment-test h3 {
  font-size: 20px;
  line-height: 40px;
  text-indent: 1em;
  border-radius: 0 10px 0 0;
  background: #fff;
}
.assessment .assessment-content .assessment-test .test-date {
  overflow-y: auto;
  height: 460px;
  border-radius: 0 0 10px 10px;
  background-color: #fff;
}
.assessment .assessment-content .assessment-test .test-date .el-tree {
  border: 0px none;
}
.assessment .assessment-content .assessment-details {
  margin-right: 20px;
  width: 900px;
  /**暂无数据时的提示***/
}
.assessment .assessment-content .assessment-details .content-title {
  margin-left: 20px;
  padding-left: 20px;
  width: calc(100% - 20px);
}
.assessment .assessment-content .assessment-details .content-title h3 {
  font-size: 20px;
}
.assessment .assessment-content .assessment-details .content-info {
  margin-left: 22px;
}
.assessment .assessment-content .assessment-details .content-info ul li {
  float: left;
  width: 33.33%;
  line-height: 50px;
  font-size: 18px;
  color: #333;
}
.assessment .assessment-content .assessment-details .last-l-t {
  margin-left: 22px;
  margin-top: 20px;
}
.assessment .assessment-content .assessment-details .content-talbe {
  height: 450px;
}
.assessment .assessment-content .assessment-details .content-talbe .star {
  margin-left: 22px;
}
.assessment .assessment-content .assessment-details .group span {
  margin-left: 22px;
  font-size: 18px;
  letter-spacing: 0.3em;
}
.assessment .assessment-content .assessment-details .liu-table {
  margin: 0 0 0 22px;
  width: 80%;
  max-height: 250px;
  /*表头背景色*/
  /*背景色高亮*/
}
.assessment .assessment-content .assessment-details .liu-table table {
  width: 100%;
  height: 100%;
  border-collapse: collapse;
}
.assessment .assessment-content .assessment-details .liu-table .headerbg {
  background-color: #eef1f6;
}
.assessment .assessment-content .assessment-details .liu-table .gray {
  background-color: #eee;
}
.assessment .assessment-content .assessment-details .liu-table .highlight {
  background-color: orange;
  box-shadow: 0 0 5px orange;
  opacity: 0.3;
  -webkit-animation-name: light;
  -webkit-animation-duration: 500ms;
  -webkit-animation-iteration-count: infinite;
  -webkit-animation-timing-function: ease-in-out;
}
.assessment .assessment-content .assessment-details .liu-table .pasehighlight {
  background-color: orange;
  box-shadow: 0 0 5px orange;
}
@-webkit-keyframes light {
0% {
    opacity: 0.1;
}
100% {
    opacity: 1;
}
}
.assessment .assessment-content .assessment-details .liu-table td,
.assessment .assessment-content .assessment-details .liu-table th {
  box-sizing: border-box;
  text-align: center;
  padding: 0px;
  border: 1px solid #ccc;
}
.assessment .assessment-content .assessment-details .liu-table tr {
  height: 45px;
}



