
.g-btn {
  position: relative;
  height: 80px;
}
.g-btn div {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.g-btn div button {
  width: 265px;
  height: 75px;
  display: inline-block;
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  padding-top: 6px;
  margin-right: 10px;
  position: relative;
  cursor: pointer;
  border-radius: 5px;
  font-weight: 500;
  border: 1px solid transparent;
  font-size: 40px;
  line-height: 1em;
}
.g-btn div .button.button-warning {
  background: #faa732;
  color: #fafafa;
}
.g-btn div .button.button-warning:hover,
.g-btn div .button.button-warning:focus {
  background: #f9960a;
  color: #fafafa;
}
.g-btn div .button.button-success {
  background: #5bb75b;
  color: #fafafa;
}
.g-btn div .button.button-success:hover,
.g-btn div .button.button-success:focus {
  background: #47a247;
  color: #fafafa;
}
.g-btn div .button-large.button-success,
.g-btn div .button.button-warning {
  box-shadow: inset 0 -4px 0 0 rgba(0, 0, 0, 0.2);
}



