
.level-two .el-dialog {
  width: 1000px;
  height: 750px;
  margin-bottom: 0;
  top: 7% !important;
}
.level-two h3 {
  font-size: 20px;
  font-weight: 600;
  margin-left: 30px;
}
.level-two ul {
  margin-top: 20px;
}
.level-two ul li {
  position: relative;
}
.level-two ul li .l-t {
  display: inline-block;
  width: 450px;
  height: 300px;
}
.level-two ul li .no-data {
  display: none;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-size: 20px;
  color: red;
  letter-spacing: 3px;
}
.level-two ul li .no-data i {
  font-size: 30px;
}
.level-two ul li .show {
  display: block;
}
.level-two ul li p {
  text-align: center;
  font-size: 18px;
  font-weight: 600;
}
@media (max-width: 1490px) {
.level-two .el-dialog {
    width: 700px;
    height: 630px;
}
.level-two ul li .l-t {
    width: 305px;
    height: 240px;
}
.level-two ul {
    margin-top: 5px;
}
}



