
.img {
  /*position: relative;**/
  width: 100%;
  height: 100%;
  /*background-color: #ccc;*/
  /**图片预览***/
}
.img i {
  font-size: 50px;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  cursor: pointer;
  color: #ccc;
}
.img .el-icon-arrow-left {
  left: 80px;
}
.img .el-icon-arrow-left:hover {
  color: #000;
}
.img .el-icon-arrow-right {
  right: 80px;
}
.img .el-icon-arrow-right:hover {
  color: #000;
}
.img .topBigimg {
  /*background-color: #337ab7;*/
  height: 550px;
  height: 80%;
  position: relative;
}
.img .topBigimg #imgp-p-s {
  height: 100%;
}
.img .topBigimg img {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  max-width: 450px;
  max-height: 450px;
  max-height: 95%;
  -webkit-transform-origin: center center;
          transform-origin: center center;
  cursor: pointer;
}
.img .warp {
  width: 800px;
  margin: auto;
  overflow: hidden;
  position: relative;
}
.img .coverright {
  position: absolute;
  right: 0px;
  top: 32px;
  width: 102px;
  height: 73px;
  z-index: 100;
  background-color: rgba(0, 0, 0, 0.5);
  box-shadow: 4px 2px 4px #888882;
}
.img .bottomsimg {
  position: relative;
  height: 125px;
  height: 20%;
  width: 490px;
  margin: auto;
  overflow: hidden;
}
.img ul {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.4);
  left: 0px;
  margin: 0 auto;
  bottom: 20px;
}
.img ul li {
  margin-right: 5px;
  margin-left: 5px;
}
.img ul li img {
  cursor: pointer;
  border: 4px solid #ccc;
  opacity: 0.4;
  /*&:hover{
					opacity:1;
				}*/
}
.img ul li img.currentImg {
  border: 4px solid #3579b7;
  opacity: 1;
}
.img .preview {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  z-index: 100;
  background-image: url('../../../../static/images/fancyboxoverlay.png');
}
.img .preview .box-preview {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  max-height: 700px;
  max-width: 900px;
  padding: 13px;
  margin: auto;
  background: #f9f9f9;
  color: #444;
  text-shadow: none;
  border-radius: 4px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.5);
}
.img .preview .box-preview a {
  position: absolute;
  cursor: pointer;
  right: -10px;
  top: -10px;
  width: 36px;
  height: 34px;
  z-index: 1000;
  background-image: url('../../../../static/images/fancybox_sprite.png');
}
.img .preview .box-preview img {
  max-height: 700px;
  max-width: 900px;
}



