
.n-l-name h3 {
  font-size: 20px;
  margin-bottom: 10px;
}
.n-l-name h3 em {
  color: red;
}
.n-l-name .table-content .el-table-column--selection .cell {
  padding: 0 5px 0 5px;
}
.n-l-name .table-content .index-td {
  border-right: 0 none;
}
.n-l-name .table-content .index-td .cell {
  text-align: center;
  padding: 0 5px 0 5px;
}
.n-l-name .btn {
  height: 50px;
  font-size: 18px;
  color: #fff;
  border-radius: 5px;
  padding: 10px;
  margin: 20px 0;
  background: #20a0ff;
  border: 1px solid #20a0ff;
  box-shadow: 3px 3px 2px #ccc;
}



