
.tingli {
  padding-left: 20px;
  /*****暂无数据*******/
  /********听力图左侧*********/
  /********听力图样式*********/
}
.tingli .noData ul > li {
  font-size: 20px;
  background-color: #65c0df;
  width: 150px;
  height: 50px;
  margin: 8px auto;
  line-height: 50px;
  text-align: center;
  margin-top: 10px;
  border-radius: 5px;
  cursor: pointer;
  color: #fff;
}
.tingli .tinglileft {
  margin-top: 30px;
  background: #65c0df;
  border: 3px solid #65c0df;
  border-radius: 0 13px 13px 13px;
  box-shadow: 1px 1px 5px #65c0df;
}
.tingli .tinglileft .tinglileftTree {
  width: 220px;
  height: 500px;
}
.tingli .tinglileft .tinglileftTree ul {
  background-color: #fff;
  padding-top: 5px;
  padding-bottom: 5px;
  border-radius: 0 10px 0 0;
}
.tingli .tinglileft .tinglileftTree li {
  font-size: 20px;
  line-height: 35px;
  margin: 0px 10px;
  height: 35px;
  text-align: center;
  border-radius: 5px;
  color: #fff;
  border: 1px solid #278bc4;
  background-color: #278bc4;
  cursor: pointer;
  box-shadow: 2px 2px 2px #ccc;
}
.tingli .tinglileft .tinglileftTree .el-tree-node__content {
  height: 40px;
  line-height: 40px;
}
.tingli .tinglileft .tinglileftTree .con-btn .el-button {
  font-size: 16px;
  padding: 8px;
  min-width: 60px;
}
.tingli .tinglileft .tinglileftTree .con-btn .el-button:focus {
  background: #1ab297;
  color: #FFF;
}
.tingli .tinglileft .tinglileftTree .test-date {
  overflow-y: auto;
  height: 453px;
  background-color: #fff;
  border-radius: 0 0 10px 10px;
}
.tingli .tinglileft .tinglileftTree .test-date .el-tree {
  border: 0px none;
}
.tingli .tingliTopTab {
  padding-top: 30px;
}
.tingli .tingliTopTab li {
  cursor: pointer;
  background-color: #eee;
  width: 110px;
  text-align: center;
  height: 40px;
  line-height: 40px;
  color: #337ab7;
  margin-bottom: 10px;
  font-size: 18px;
  border-radius: 5px 0 0 5px;
  box-shadow: 3px 3px 2px #ccc;
}
.tingli .tingliTopTab li.active {
  background-color: #65c0df;
  color: #fff;
}
.tingli .tingliright {
  padding-top: 30px;
  width: 900px;
  height: 90%;
  /*****听力图片*****/
}
.tingli .tingliright .ear {
  width: 100%;
  height: 100%;
}
.tingli .tingliright .tingli-warp {
  margin-left: 20px;
  height: 100%;
}
.tingli .tingliright .xq {
  line-height: 47px;
  font-size: 18px;
  text-indent: 1em;
}
.tingli .tingliright .content-title {
  width: calc(100% - 20px);
}
.tingli .tingliright .tinglirightImg {
  height: 100%;
  position: relative;
}
.tingli .tingliright .tinglirightImg .img-preview {
  height: calc(100% - 52px);
}
.tingli .tingliright .tinglirightImg .img .topBigimg img {
  max-width: 450px;
}
.tingli .tingliright .tinglirightImg .QQcode {
  position: absolute;
  right: 42px;
  top: 130px;
  width: 120px;
  height: 150px;
}
.tingli .tingliright .tinglirightImg .QQcode .QQcodeinner {
  height: 130px;
  width: 120px;
  background-color: #37c438;
  border-radius: 10px;
  position: absolute;
  left: 50%;
  top: 20px;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  color: #fff;
  text-align: center;
}
.tingli .tingliright .tinglirightImg .QQcode .QQcodeinner div {
  width: 90px;
  height: 90px;
  background-color: #fff;
  margin-left: 15px;
  margin-top: 15px;
}
.tingli .tingliright .tinglirightImg .QQcode p {
  margin-top: 150px;
  color: #37c438;
  font-size: 14px;
}
.tingli .tingliright .tinglirightImg .QQcode ul > li {
  background-color: #65c0df;
  width: 120px;
  height: 35px;
  margin-left: 8px;
  line-height: 35px;
  text-align: center;
  margin-top: 15px;
  border-radius: 5px;
  cursor: pointer;
  color: #fff;
}
.tingli .editSvg .el-dialog {
  padding-bottom: 30px;
  width: 1300px;
}



