
.header-info .header-st-info {
  height: 80px;
  padding: 20px 20px 14px 20px;
  border-top: 6px solid #eeeeee;
  border-bottom: 10px solid #eeeeee;
  background-color: #fff;
}
.header-info .header-st-info li {
  list-style: none;
}
.header-info .header-st-info .img-name {
  float: left;
  font-size: 25px;
  width: 200px;
  overflow: hidden;
  border-right: 1px solid #cbcbcb;
}
.header-info .header-st-info .img-name img {
  float: left;
  height: 80px;
  width: 80px;
  border-radius: 50%;
}
.header-info .header-st-info .img-name div {
  height: 80px;
  float: left;
  margin-left: 20px;
}
.header-info .header-st-info .img-name div i {
  font-size: 18px;
}
.header-info .header-st-info .img-name .name {
  line-height: 40px;
  display: block;
  width: 4em;
  overflow: hidden;
  /*自动隐藏文字*/
  text-overflow: ellipsis;
  /*文字隐藏后添加省略号*/
  white-space: nowrap;
  /*强制不换行*/
}
.header-info .header-st-info .equipment {
  float: left;
  font-weight: 600;
  margin-left: 20px;
}
.header-info .header-st-info .equipment span {
  width: 100px;
  letter-spacing: 2px;
  font-size: 16px;
  background-color: #777777;
  border-radius: 20px;
  text-align: right;
  padding: 0px 10px;
  color: #fff;
}
.header-info .header-st-info .equipment p {
  line-height: 27px;
  margin-left: 10px;
  color: #333;
  font-size: 16px;
}
.header-info .header-st-info .equipment p em {
  display: inline-block;
  width: 120px;
  text-align: right;
  padding-right: 10px;
  font-weight: 600;
}
.header-info .header-st-info .equipment span,
.header-info .header-st-info .equipment p {
  height: 30px;
  line-height: 30px;
  float: left;
}
.header-info .header-st-info .equipment li {
  margin-bottom: 10px;
}
.header-info .header-st-info .equipment ul {
  margin: 0px;
}
.header-info .header-st-info .t-e {
  float: left;
  margin-left: 100px;
}
.header-info .header-st-info .text {
  padding: 5px;
  border-radius: 3px;
  border: 1px solid #ccc;
  box-sizing: border-box;
  width: 20%;
  min-width: 280px;
  color: #333;
  height: 75px;
  overflow: auto;
  word-wrap: break-word;
  font-size: 16px;
  letter-spacing: 2px;
}



