
.help h3 a[data-v-5b3a1c06] {
  font-size: 22px;
  letter-spacing: 3px;
  color: #000;
  padding-left: 1em;
}
.help .help-content[data-v-5b3a1c06] {
  width: 100%;
  color: #000;
}
.help .list[data-v-5b3a1c06] {
  width: 25%;
  font-size: 18px;
  margin-right: 20px;
  /*图标旋转*/
}
.help .list dl[data-v-5b3a1c06] {
  max-height: 400px;
  overflow: auto;
}
.help .list dt[data-v-5b3a1c06],
.help .list dd[data-v-5b3a1c06] {
  margin: 5px 0;
  line-height: 40px;
  padding: 0;
  padding-left: 1em;
  /*cursor: pointer;*/
}
.help .list dt a[data-v-5b3a1c06],
.help .list dd a[data-v-5b3a1c06] {
  display: block;
  text-decoration: none;
  color: #000;
}
.help .list dt[data-v-5b3a1c06] {
  position: relative;
}
.help .list dt i[data-v-5b3a1c06] {
  position: absolute;
  left: 130px;
  top: 10px;
}
.help .list .ion[data-v-5b3a1c06] {
  -webkit-animation: i-rotate 0.3s;
          animation: i-rotate 0.3s;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
@-webkit-keyframes i-rotate {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
60% {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
}
100% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}
}
@keyframes i-rotate {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
60% {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
}
100% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}
}
.help .list .no-i[data-v-5b3a1c06] {
  -webkit-animation: i-no-rotate 0.3s;
          animation: i-no-rotate 0.3s;
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
}
@-webkit-keyframes i-no-rotate {
0% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}
60% {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
}
100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
}
@keyframes i-no-rotate {
0% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}
60% {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
}
100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
}
.help .list dd[data-v-5b3a1c06] {
  font-size: 16px;
  margin-left: 1em;
}
.help .list .on[data-v-5b3a1c06] {
  background-color: #ccc;
}
.help section[data-v-5b3a1c06] {
  width: 70%;
}
.help section p[data-v-5b3a1c06] {
  font-size: 20px;
  margin: 10px 0;
}
.help section article[data-v-5b3a1c06] {
  max-height: 400px;
  overflow: auto;
  font-size: 16px;
}



