/*控制音量大小*/
.voice-num {
  margin: 0px;
  margin-left: 30px;
}
.voice-num p {
  height: 59px;
  font-size: 20px;
  text-align: center;
  line-height: 59px;
  padding: 0 15px;
  color: #fff;
  border: 1px solid #42d885;
  border-radius: 4px 0 0 4px;
  border-bottom: 0px none;
  background-color: #42d885;
}
.voice-num .num {
  width: 250px;
  font-size: 20px;
}
.voice-num .num span {
  line-height: 60px;
}
.voice-num .num .el-input-number__decrease {
  right: 0;
  left: 0;
  width: 85px;
  border-left: 0px none;
  border-right: 1px solid #bfcbd9;
}
.voice-num .num .el-input-number__increase {
  width: 85px;
}
.voice-num .num .el-input__inner {
  text-align: center;
  height: 60px;
  border-radius: 0 4px 4px 0;
  padding-right: 0;
  border-left: 0px none;
}
.voice-num .num .el-input {
  font-size: 20px;
}



