
@import "../../../common/style/css/icon.css";
.result {
  /********打印样式*********/
}
.result #QR {
  float: right;
  width: 150px;
  margin: 20px 50px 0 0;
}
.result .res-dialog > .el-dialog {
  width: 1200px;
  height: 708px;
  margin-bottom: 0;
}
.result .res-dialog > .el-dialog > .el-dialog__title {
  font-size: 18;
  font-weight: 600 ;
  margin-left: 30px;
}
.result .res-dialog > .el-dialog > .el-dialog__body {
  padding: 5px 20px 14px 20px;
  overflow: auto;
  font-size: 12px;
  background: #fff;
}
.result .tips {
  color: #666;
  font-size: 30px;
  text-align: center;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.result .table #main-table-A,
.result .table #main-table-R,
.result .table #main-table-L {
  /*单个测听结果的样式*/
  width: 46%;
  margin: 10px;
  padding: 10px;
  height: 512px;
}
.result .table #main-table-A.marginleft,
.result .table #main-table-R.marginleft,
.result .table #main-table-L.marginleft {
  margin-left: 25%;
}
.result .table #main-table-A h3,
.result .table #main-table-R h3,
.result .table #main-table-L h3 {
  margin: 0px;
  text-align: center;
  /*color:red;*/
}
.result .table #main-table-A ul,
.result .table #main-table-R ul,
.result .table #main-table-L ul {
  margin: 0px;
  padding: 0px;
  float: left;
  width: 30px;
  text-align: center;
  margin-top: 37px;
}
.result .table #main-table-A ul li,
.result .table #main-table-R ul li,
.result .table #main-table-L ul li {
  list-style: none;
  line-height: 17px;
  font-size: 15px;
  font-weight: 400;
}
.result .table #main-table-A #A,
.result .table #main-table-R #A,
.result .table #main-table-L #A,
.result .table #main-table-A #L,
.result .table #main-table-R #L,
.result .table #main-table-L #L,
.result .table #main-table-A #R,
.result .table #main-table-R #R,
.result .table #main-table-L #R {
  width: 94%;
  border-collapse: collapse;
}
.result .table #main-table-A #A tr th,
.result .table #main-table-R #A tr th,
.result .table #main-table-L #A tr th,
.result .table #main-table-A #L tr th,
.result .table #main-table-R #L tr th,
.result .table #main-table-L #L tr th,
.result .table #main-table-A #R tr th,
.result .table #main-table-R #R tr th,
.result .table #main-table-L #R tr th,
.result .table #main-table-A #A tr td,
.result .table #main-table-R #A tr td,
.result .table #main-table-L #A tr td,
.result .table #main-table-A #L tr td,
.result .table #main-table-R #L tr td,
.result .table #main-table-L #L tr td,
.result .table #main-table-A #R tr td,
.result .table #main-table-R #R tr td,
.result .table #main-table-L #R tr td {
  box-sizing: border-box;
  padding: 0px;
  border: 1px solid #ccc;
}
.result .table #main-table-A #A tr th,
.result .table #main-table-R #A tr th,
.result .table #main-table-L #A tr th,
.result .table #main-table-A #L tr th,
.result .table #main-table-R #L tr th,
.result .table #main-table-L #L tr th,
.result .table #main-table-A #R tr th,
.result .table #main-table-R #R tr th,
.result .table #main-table-L #R tr th {
  height: 15px;
}
.result .table #main-table-A #A tr td:nth-of-type(2n-1),
.result .table #main-table-R #A tr td:nth-of-type(2n-1),
.result .table #main-table-L #A tr td:nth-of-type(2n-1),
.result .table #main-table-A #L tr td:nth-of-type(2n-1),
.result .table #main-table-R #L tr td:nth-of-type(2n-1),
.result .table #main-table-L #L tr td:nth-of-type(2n-1),
.result .table #main-table-A #R tr td:nth-of-type(2n-1),
.result .table #main-table-R #R tr td:nth-of-type(2n-1),
.result .table #main-table-L #R tr td:nth-of-type(2n-1) {
  border-right: 1px solid transparent;
}
.result .table #main-table-A #A tr td:nth-of-type(2n),
.result .table #main-table-R #A tr td:nth-of-type(2n),
.result .table #main-table-L #A tr td:nth-of-type(2n),
.result .table #main-table-A #L tr td:nth-of-type(2n),
.result .table #main-table-R #L tr td:nth-of-type(2n),
.result .table #main-table-L #L tr td:nth-of-type(2n),
.result .table #main-table-A #R tr td:nth-of-type(2n),
.result .table #main-table-R #R tr td:nth-of-type(2n),
.result .table #main-table-L #R tr td:nth-of-type(2n) {
  border-left: 1px solid transparent;
}
.result .table #main-table-A #A tr span,
.result .table #main-table-R #A tr span,
.result .table #main-table-L #A tr span,
.result .table #main-table-A #L tr span,
.result .table #main-table-R #L tr span,
.result .table #main-table-L #L tr span,
.result .table #main-table-A #R tr span,
.result .table #main-table-R #R tr span,
.result .table #main-table-L #R tr span {
  text-align: center;
  width: 100%;
  display: block;
}
.result .table #main-table-A #A tr .spanborder,
.result .table #main-table-R #A tr .spanborder,
.result .table #main-table-L #A tr .spanborder,
.result .table #main-table-A #L tr .spanborder,
.result .table #main-table-R #L tr .spanborder,
.result .table #main-table-L #L tr .spanborder,
.result .table #main-table-A #R tr .spanborder,
.result .table #main-table-R #R tr .spanborder,
.result .table #main-table-L #R tr .spanborder {
  border-right: 1px solid #666;
}
.result .table #main-table-A #A tr .bgcspan,
.result .table #main-table-R #A tr .bgcspan,
.result .table #main-table-L #A tr .bgcspan,
.result .table #main-table-A #L tr .bgcspan,
.result .table #main-table-R #L tr .bgcspan,
.result .table #main-table-L #L tr .bgcspan,
.result .table #main-table-A #R tr .bgcspan,
.result .table #main-table-R #R tr .bgcspan,
.result .table #main-table-L #R tr .bgcspan {
  background-color: #66cc66;
}
.result .table #main-table-A {
  border: 1px solid #000;
}
.result .table #main-table-A h3 {
  color: #000;
}
.result .table #main-table-L {
  border: 1px solid blue;
}
.result .table #main-table-L h3 {
  color: blue;
}
.result .table #main-table-R {
  border: 1px solid red;
}
.result .table #main-table-R h3 {
  color: red;
}
.result .result-btn {
  text-align: center;
  margin-top: 30px;
  margin-left: 0;
  position: fixed;
  bottom: 20px;
  /*left: 50%;*/
  /*transform: translate(25%);*/
}
.result .result-btn button {
  width: 130px;
  margin-left: 50px;
  letter-spacing: 2px;
}
.result .result-btn .el-button--large {
  padding: 10px 0px;
  font-size: 18px;
}
.result .result-btn .el-button--large i {
  font-size: 20px;
  margin-right: 5px;
}
.result .result-btn .el-button--primary {
  background-color: #25c6ca;
  border-color: #25c6ca;
}
.result .result-btn #back-btn .button-large {
  height: 45px;
  padding: 0 10px;
  border-radius: 6px;
  font-size: 20px;
  line-height: 42px;
}
.result .result-btn #back-btn .back {
  margin: 0 0 0 70px;
}
.result .print .print-warp {
  width: 230mm;
  height: 190mm;
  margin-left: 150px;
  background-color: #ccc;
  overflow-y: auto;
  position: fixed;
  padding: 10px 0px;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
}
.result .print .close {
  top: 0;
}
.result .print .affirm {
  top: 45px;
}
.result .print .visibility .print-warp {
  height: 150mm;
}



