
.printer-progress {
  /*主要内容*/
}
.printer-progress .el-dialog {
  margin-bottom: 0;
  border: 0 none;
  border-radius: 10px;
  width: 600px;
}
.printer-progress .el-dialog .el-dialog__header {
  border: 0 none;
}
.printer-progress .el-dialog .el-dialog__body {
  border-radius: 10px;
  border: 3px solid #20a0ff;
  max-height: 450px;
}
.printer-progress .content {
  /*按钮*/
}
.printer-progress .content h3 {
  font-size: 24px;
  color: #20a0ff;
  text-align: center;
}
.printer-progress .content p {
  font-size: 20px;
  padding-left: 30px;
  margin: 15px 0;
}
.printer-progress .content p span {
  color: red;
}
.printer-progress .content p em {
  color: blue;
}
.printer-progress .content .btn {
  float: right;
}
.printer-progress .content .select-warp {
  margin: 0 auto;
  width: 230px;
}



