
#tone-audiometry {
  width: 1330px;
  height: 600px;
  margin: 80px auto;
}
#tone-audiometry .audiometry-right {
  width: 420px;
  background-color: #fff;
  height: 100%;
  border: 6px solid #5e9b71;
}
#tone-audiometry .audiometry-right .active {
  background-color: #5c946c;
  color: #fff;
}
#tone-audiometry .audiometry-right .title {
  border-bottom: 3px solid #5e9b71;
}
#tone-audiometry .audiometry-right .title h3 {
  margin: 0px;
  margin-left: 30px;
  line-height: 50px;
  font-size: 20px;
}
#tone-audiometry .audiometry-right dt,
#tone-audiometry .audiometry-right h3,
#tone-audiometry .audiometry-right .demonstration {
  color: #579b6a;
}
#tone-audiometry .audiometry-right .audiometry-main {
  margin-top: 30px;
}
#tone-audiometry .audiometry-right .button {
  margin: 130px 88px;
}
#tone-audiometry .audiometry-right .button ul,
#tone-audiometry .audiometry-right .button li {
  list-style: none;
  margin: 0px;
  padding: 0px;
}
#tone-audiometry .audiometry-right .button .el-button + .el-button {
  margin-left: 0px;
}
#tone-audiometry .audiometry-right .button button {
  width: 260px;
  height: 60px;
  margin-bottom: 10px;
  border-radius: 5px;
  cursor: pointer;
  color: #fff;
  text-align: center;
  font-size: 24px;
}
#tone-audiometry .audiometry-left {
  width: 770px;
  border: 6px solid #5e9b71;
  height: 600px;
  background-color: #fff;
}
#tone-audiometry .audiometry-left .tone-top {
  font-size: 20px;
  margin: 30px 70px 15px 70px;
  color: #333;
}
#tone-audiometry .audiometry-left .tone-table {
  margin: 0px auto;
  width: 90%;
  min-height: 140px;
  /*表头背景色*/
  /*背景色高亮*/
}
#tone-audiometry .audiometry-left .tone-table table {
  width: 100%;
  height: 100%;
  border-collapse: collapse;
}
#tone-audiometry .audiometry-left .tone-table .headerbg {
  background-color: #eef1f6;
}
#tone-audiometry .audiometry-left .tone-table .gray {
  background-color: #eee;
}
#tone-audiometry .audiometry-left .tone-table .highlight {
  background-color: orange;
  box-shadow: 0 0 5px orange;
  opacity: 0.3;
  -webkit-animation-name: light;
  -webkit-animation-duration: 500ms;
  -webkit-animation-iteration-count: infinite;
  -webkit-animation-timing-function: ease-in-out;
}
#tone-audiometry .audiometry-left .tone-table .pasehighlight {
  background-color: orange;
  box-shadow: 0 0 5px orange;
}
@-webkit-keyframes light {
0% {
    opacity: 0.1;
}
100% {
    opacity: 1;
}
}
#tone-audiometry .audiometry-left .tone-table td,
#tone-audiometry .audiometry-left .tone-table th {
  box-sizing: border-box;
  text-align: center;
  padding: 0px;
  border: 1px solid #ccc;
}
#tone-audiometry .audiometry-left .tone-table td span {
  display: inline-block;
  word-wrap: break-word;
  width: 88px;
}
#tone-audiometry .audiometry-left .tone-table tr {
  height: 45px;
}
#tone-audiometry .tips .el-dialog__header,
#tone-audiometry .tips .el-dialog__body {
  padding: 0px;
}
#tone-audiometry .tips img {
  width: 100%;
}



