  /*表头背景色*/
  /*评价*/
}
.tone-table table[data-v-7d4e5a42] {
  width: 100%;
  border-collapse: collapse;
}
.tone-table .headerbg[data-v-7d4e5a42] {
  background-color: #eef1f6;
}
