}
.tone-table td[data-v-7d4e5a42],
.tone-table th[data-v-7d4e5a42] {
  box-sizing: border-box;
  text-align: center;
  padding: 0px;
  border: 1px solid #ccc;
  width: 50px;
}
.tone-table tr[data-v-7d4e5a42] {
  height: 35px;
}
.tone-table .star[data-v-7d4e5a42] {
