}
.tone-table .star i[data-v-7d4e5a42] {
  margin-left: 10px;
}
.last-l-t[data-v-7d4e5a42] {
  /*margin-left:70px;*/
  margin-top: 10px;
}
/*表格的排列样式*/
.mormTable[data-v-7d4e5a42] {
  width: 49%;
