
#add-zhu #remarks {
  border-radius: 5px;
  resize: none;
  width: 300px;
  margin-top: 10px;
  margin-left: 100px;
}
#add-zhu .res {
  font-size: 16px !important;
}
#add-zhu .res-left > div {
  margin-top: 10px;
  margin-left: 20px;
}
#add-zhu .res-left > div .degree .el-input {
  width: 65%;
}
#add-zhu .res-left > div span {
  height: 24px;
  line-height: 24px;
}
#add-zhu .res-left > div .z-degree {
  width: 110px;
}
#add-zhu .res-left > div input {
  height: 26px;
}
#add-zhu .res-left > div div {
  margin-left: 0px;
}
#add-zhu .res-left > div div .el-date-editor.el-input {
  width: 182px;
}
#add-zhu .res-left > div div .el-input__inner {
  height: 32px;
  border-radius: 0px;
  border: 1px solid #a9a9a9;
}
#add-zhu .res-left > div div .el-input__inner:focus {
  border: 1px solid #a9a9a9;
}
#add-zhu .res-right {
  margin-right: 20px;
}
#add-zhu .res-right td {
  margin-left: 40px;
}
#add-zhu .res-right td input {
  width: 50px;
  height: 25px;
}
#add-zhu .res-right td .el-input__inner {
  height: 32px;
  width: 100px;
  border-radius: 0px;
  border: 1px solid #a9a9a9;
}
#add-zhu .res-right span {
  display: inline-block;
  line-height: 24px;
  height: 24px;
  margin-right: 15px;
}
#add-zhu textarea,
#add-zhu input {
  outline: none;
  padding-left: 10px;
}
#add-zhu .bigCanvas {
  /***display:none;***/
  position: absolute;
  padding: 10px;
  left: 35px;
  top: 40px;
  width: 1140px;
  height: 580px;
  border-top: 1px solid #ccc;
  background-color: #fff;
}
#add-zhu .bigCanvas-on {
  display: block;
}
#add-zhu .ibox-title {
  position: relative;
}
#add-zhu .ibox-title div {
  position: absolute;
  font-size: 18px;
  border-radius: 5px;
  padding: 3px;
  cursor: pointer;
  top: -10px;
}
#add-zhu .ibox-title .l-edit {
  left: 420px;
  color: red;
  border: 1px solid red;
}
#add-zhu .ibox-title .f-edit {
  right: 420px;
  color: #0000FF;
  border: 1px solid #0000FF;
}
#add-zhu .ibox-title .r-recovery {
  right: -65px;
  border: 1px solid #ccc;
  margin-top: 70px;
}
#add-zhu .big-btn {
  position: absolute;
  z-index: 20002;
  right: 8px;
  top: 100px;
}
#add-zhu .big-btn ul li {
  margin: 10px 0;
}



