
.check-title {
  position: absolute;
  left: 30px;
  top: -90px;
  width: 300px;
  height: 80px;
  background: url(../../../../static/images/new-title.png) no-repeat 3px -98px;
}
.add-class {
  /*选择班级时，的默认样式*/
}
.add-class .el-dialog--small {
  height: 500px;
  width: 650px;
}
.add-class .el-dialog--small .el-dialog__body {
  margin: 50px 30px 0px 20px;
  position: relative;
  padding: 0;
}
.add-class ul {
  height: 500px;
  overflow-y: auto;
}
.add-class li {
  cursor: pointer;
  float: left;
  height: 40px;
  width: 95px;
  line-height: 40px;
  border-radius: 3px;
  margin: 5px;
  padding: 3px;
  text-align: center;
  font-size: 18px;
  color: #fff;
  background-color: #169bd5;
  box-shadow: 3px 3px 3px #ccc;
}
.add-class li:hover {
  background-color: #1772f9;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.add-class .add-new-class {
  background-color: #13ce66;
  box-shadow: 3px 3px 3px #ccc;
}
.add-class .add-new-class:hover {
  background-color: #13ce66;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.add-class .checked-class {
  color: #333;
  background-color: #13ce66;
  box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.5);
}
.el-message-box__wrapper .add-new {
  /*border:3px solid #109bff;*/
  box-shadow: 0 0 30px #000;
  border-radius: 15px;
  position: absolute;
  left: 50%;
  top: 160px;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
}
.el-message-box__wrapper .add-new .el-message-box__wrapper {
  top: 300px;
}
.el-message-box__wrapper .add-new .el-message-box__header {
  display: none;
}



