
#add-svg {
  height: 650px;
  margin: 0 auto;
  /******测试方法选择******/
  /*******放大图的按钮组********/
}
#add-svg .test-add-model {
  line-height: 30px;
  font-size: 18px;
  margin-bottom: 10px;
}
#add-svg .test-add-model li {
  float: left;
  margin-left: 20px;
  /**********助听设备***********/
  /******测试方法***********/
}
#add-svg .test-add-model li input {
  height: 20px;
}
#add-svg .test-add-model li .el-input {
  width: 160px;
}
#add-svg .test-add-model li .el-input__inner {
  height: 26px !important;
  border-radius: 0px;
  width: 160px !important;
  padding-left: 5px !important;
}
#add-svg .test-add-model li .el-input__inner:focus {
  border: 1px solid #a9a9a9;
}
#add-svg .big-btn {
  position: absolute;
  z-index: 20002;
  right: 35px;
  top: 100px;
  /****单选组*****/
}
#add-svg .big-btn ul li {
  margin: 10px 0;
}
#add-svg .big-btn .el-radio-button__inner {
  border-radius: 4px;
  border: 1px solid #bfcbd9;
  padding: 10px 0;
  width: 100%;
}
#add-svg .big-btn .el-radio-button {
  display: block;
  margin: 10px 0;
}
#add-svg .big-btn .el-radio-group {
  margin: 15px 0;
}
#add-svg .big-btn .el-button {
  padding: 10px 0;
  width: 100%;
}
#add-svg .tl-data-table tr input {
  height: 22px;
  padding-left: 3px;
  margin: 0;
  width: 25px;
}
/******无背景图******/
.ibox-title-row {
  position: relative;
  cursor: pointer;
  width: 120px;
  height: 35px;
  line-height: 35px;
  text-align: center;
  border: 1px solid #ccc;
}
.ibox-title-row:hover {
  color: #20a0ff;
}
.ibox-title-row .arrows {
  font-size: 14px;
  position: absolute;
  right: 5px;
  top: 12px;
  -webkit-transform: rotate(0);
          transform: rotate(0);
  transition: -webkit-transform 0.4s ease-in-out;
  transition: transform 0.4s ease-in-out;
  transition: transform 0.4s ease-in-out, -webkit-transform 0.4s ease-in-out;
}
.ibox-title-row .arrowsActive {
  right: 3px;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.addtu {
  /******放大图******/
}
.addtu .ibox-content {
  padding-top: 10px;
}
.addtu .c-btn {
  margin-left: 10px;
  width: 10%;
  margin-top: 100px;
}
.addtu .btn-group-lg li {
  margin-top: 10px;
  /******复制到另一侧*****/
}
.addtu .btn-group-lg li button {
  width: 120px;
  height: 40px;
  cursor: pointer;
}
.addtu #save {
  width: 100px;
  height: 40px;
  cursor: pointer;
  color: #fff;
  background-color: #22c6c6;
  font-size: 18px;
  margin: 30px;
}
.addtu .row-info {
  padding: 0 20px;
}
.addtu .row-info li {
  margin-left: 0px;
}
.addtu .row-info li .el-date-editor.el-input {
  width: 182px;
}
.addtu .row-info li .el-input__inner {
  height: 26px;
  border-radius: 0px;
  border: 1px solid #a9a9a9;
}
.addtu .row-info li .el-input__inner:focus {
  border: 1px solid #a9a9a9;
}
.addtu .row-info .degree .el-input {
  width: 70%;
}
.addtu .row-marks {
  margin: 10px 40px;
}
.addtu .row-marks textarea {
  width: 300px !important;
  font-size: 16px;
  padding-left: 5px;
}
.addtu .row ul {
  margin-top: 14px;
}
.addtu .row ul li {
  margin-top: 10px;
}
.addtu .row tr .el-input .el-input__inner {
  height: 28px !important;
  border-radius: 0px;
  border: 1px solid #a9a9a9;
  width: 100px !important;
  padding-left: 5px !important;
}
.addtu .row tr .el-input .el-input__inner:focus {
  border: 1px solid #a9a9a9;
}
.addtu .clear {
  clear: both;
}
.addtu .bigCanvas {
  /***display:none;***/
  position: absolute;
  padding: 10px;
  left: 0px;
  top: 0px;
  width: 1280px;
  height: 700px;
  border-top: 1px solid #ccc;
  background-color: #fff;
}
.addtu .bigCanvas-on {
  display: block;
}



