
.gameType dl .el-radio-button__orig-radio:checked + .el-radio-button__inner {
  background-color: #13ce66;
  border: 1px solid #13ce66;
  box-shadow: -1px 0 0 0 #13ce66;
  color: #fff;
}
.gameType dl.games dd {
  float: left;
  margin-left: 40px;
}
.gameType dl.games dt {
  margin-top: 45px;
}
.gameType dl.games .img {
  position: relative;
  margin-bottom: 5px;
  cursor: pointer;
}
.gameType dl.games span {
  margin-left: 20px;
}
.gameType dl.games p {
  display: none;
  width: 100px;
  height: 100px;
  margin: 0px;
  border-radius: 50%;
  background-color: rgba(0, 0, 0, 0.5);
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.gameType dl.games p i {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-size: 40px;
  color: #fff;
}
.gameType dl.games .activeImg p {
  display: block;
}
.gameType dd .active {
  background-color: #13ce66;
  color: #fff;
}
.gameType dd img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: 5px solid #e6f0e5;
  box-shadow: 1px 1px 1px;
  display: block;
}



