
.especialParams {
  width: 1150px;
  height: 700px;
  margin: 0px auto ;
  margin-top: 60px;
  background-color: #fff;
  border: 8px solid #5f9970;
}
.especialParams .params_content {
  /*background-color: #ccc;*/
  width: 70%;
  height: 300px;
  margin: 150px auto 0px;
  /*验证提示*/
  /*字体背景田字格*/
  /*验证不通过时的样式*/
}
.especialParams .params_content .spanInner {
  margin-right: 30px;
  line-height: 90px;
  color: #ff991f;
  font-size: 30px;
  text-align: center;
}
.especialParams .params_content .el-input__inner {
  padding: 3px 0 3px 30px;
}
.especialParams .params_content .el-form-item__error {
  font-size: 14px;
  left: 15px;
}
.especialParams .params_content .params_content_div {
  width: 550px;
  margin: 0 auto;
}
.especialParams .params_content .params_content_div_list {
  position: relative;
  padding: 0px 20px;
}
.especialParams .params_content .params_content_div_list .inputWidth,
.especialParams .params_content .params_content_div_list .el-input {
  width: 100px;
  font-size: 30px;
}
.especialParams .params_content .el-form .el-input__inner {
  height: 90px;
}
.especialParams .params_content .background-img {
  position: absolute;
  width: 100px;
  height: 100px;
  right: 12px;
  top: 2px;
  background: url('../../../../static/images/coin.jpg') no-repeat center;
}
.especialParams .params_content .imgLeft {
  right: 14px;
}
.especialParams .params_content input {
  width: 76px;
  height: 100px;
  border-radius: 5px;
  border: 1px solid #ccc;
  font-size: 40px;
  padding-left: 33px;
  background: cornsilk;
  background: rgba(255, 252, 238, 0.5);
  color: navy;
}
.especialParams .params_content .is-error input {
  border: 1px solid red;
}
.especialParams .params_content .is-error input:focus {
  border: 1px solid red;
  outline-offset: 0px;
}
.especialParams .params_content input:focus {
  border: 1px solid #20a0ff;
  outline-offset: 0px;
}
.especialParams p {
  font-size: 34px;
  color: #ff991f;
  font-weight: 700;
  text-align: center;
  margin: 50px 0px;
}
.especialParams .params_content_next {
  margin-top: 60px;
  text-align: center;
}
.especialParams .params_content_next .el-button {
  font-size: 30px;
  width: 150px;
}



