
#audiometry {
  width: 1330px;
  margin: 5px auto;
  overflow: hidden;
  /*跳过hz选择db*/
}
#audiometry .audiometry-right {
  width: 420px;
  background-color: #fff;
  /*margin-top: 190px;*/
  /*height: 740px;*/
  border: 6px solid #5e9b71;
}
#audiometry .audiometry-right .active {
  background-color: #5c946c;
  color: #fff;
}
#audiometry .audiometry-right .title {
  border-bottom: 3px solid #5e9b71;
}
#audiometry .audiometry-right .title h3 {
  margin: 0px;
  margin-left: 30px;
  line-height: 50px;
  font-size: 20px;
}
#audiometry .audiometry-right dt,
#audiometry .audiometry-right h3,
#audiometry .audiometry-right .demonstration {
  color: #579b6a;
}
#audiometry .audiometry-right .audiometry-main dl {
  margin-top: 20px;
}
#audiometry .audiometry-right .audiometry-main dt,
#audiometry .audiometry-right .audiometry-main .demonstration {
  float: left;
  margin-left: 65px;
  margin-top: 4px;
  font-size: 22px;
}
#audiometry .audiometry-right .audiometry-main dd {
  float: left;
  height: 35px;
  line-height: 35px;
  font-size: 18px;
}
#audiometry .audiometry-right .audiometry-main .demonstration {
  margin-left: 0px;
  margin-top: 10px;
}
#audiometry .audiometry-right .audiometry-main .num {
  width: 200px;
  font-size: 20px;
}
#audiometry .audiometry-right .audiometry-main .num span {
  line-height: 50px;
}
#audiometry .audiometry-right .audiometry-main .num .el-input-number__decrease,
#audiometry .audiometry-right .audiometry-main .num .el-input-number__increase {
  width: 60px;
}
#audiometry .audiometry-right .audiometry-main .num .el-input__inner {
  height: 50px;
  border-left: 1px solid #bfcbd9;
  border-radius: 4px;
}
#audiometry .audiometry-right .button {
  margin: 90px 88px 0 88px;
}
#audiometry .audiometry-right .button ul,
#audiometry .audiometry-right .button li {
  list-style: none;
  margin: 0px;
  padding: 0px;
}
#audiometry .audiometry-right .button .el-button + .el-button {
  margin-left: 0px;
}
#audiometry .audiometry-right .button button {
  width: 260px;
  height: 60px;
  /*line-height: 60px;*/
  margin-bottom: 10px;
  border-radius: 5px;
  cursor: pointer;
  color: #fff;
  text-align: center;
  /*background: #0ddd47;*/
  font-size: 24px;
}
#audiometry .audiometry-left {
  width: 770px;
}
#audiometry .audiometry-left .audiometry-left-top {
  height: 190px;
  /*background: #fff;*/
  position: relative;
}
#audiometry .audiometry-left .audiometry-left-top div {
  position: absolute;
  text-indent: -9999px;
}
#audiometry .audiometry-left .audiometry-left-top div:nth-child(1) {
  width: 88px;
  height: 127px;
  background: url(../../../../static/images/audio.png) no-repeat 0 0;
  bottom: 0px;
  left: 60px;
}
#audiometry .audiometry-left .audiometry-left-top div:nth-child(2) {
  width: 200px;
  height: 35px;
  background: url(../../../../static/images/left.png) no-repeat 0 0;
  bottom: 65px;
  left: 125px;
}
#audiometry .audiometry-left .audiometry-left-top div:nth-child(3) {
  width: 150px;
  height: 190px;
  background: url(../../../../static/images/child.png) no-repeat 0 0;
  background-size: 150px 195px;
  bottom: 0px;
  left: 300px;
}
#audiometry .audiometry-left .audiometry-left-top div:nth-child(4) {
  width: 200px;
  height: 35px;
  background: url(../../../../static/images/right.png) no-repeat 0 0;
  bottom: 65px;
  right: 125px;
  z-index: 100;
}
#audiometry .audiometry-left .audiometry-left-top div:nth-child(5) {
  width: 88px;
  height: 127px;
  background: url(../../../../static/images/audio.png) no-repeat 0 0;
  bottom: 0px;
  right: 60px;
}
#audiometry .audiometry-left .audiometry-table {
  height: 570px;
  border: 6px solid #5e9b71;
  background: #fff;
}
#audiometry .audiometry-left .audiometry-table .table-HZ {
  position: relative;
  height: 120px;
  background: -webkit-gradient(linear, 0% 0%, 0% 100%, from(#41484e), to(#000));
  /*切换频率定位*/
  /*切换频率弹框*/
}
#audiometry .audiometry-left .audiometry-table .table-HZ .toggleHz {
  position: absolute;
  right: -93px;
  bottom: -39px;
}
#audiometry .audiometry-left .audiometry-table .table-HZ .el-dialog {
  top: 40%;
  width: 1000px;
}
#audiometry .audiometry-left .audiometry-table .table-HZ .el-dialog .el-dialog__header {
  text-align: center;
  font-size: 18px;
}
#audiometry .audiometry-left .audiometry-table .table-HZ .el-dialog .el-dialog__body {
  padding-top: 0px;
}
#audiometry .audiometry-left .audiometry-table .table-HZ ul {
  margin: 0px;
}
#audiometry .audiometry-left .audiometry-table .table-HZ ul > li {
  padding-top: 10px;
  width: 40%;
  float: left;
  list-style: none;
  color: #01ff30;
  text-align: center;
}
#audiometry .audiometry-left .audiometry-table .table-HZ ul > li:nth-child(1) {
  width: 20%;
}
#audiometry .audiometry-left .audiometry-table .table-HZ ul > li:nth-child(2) strong {
  border-right: 3px solid #01ff30;
}
#audiometry .audiometry-left .audiometry-table .table-HZ strong {
  display: block;
  font-weight: 500;
  font-size: 50px;
}
#audiometry .audiometry-left .audiometry-table .height-90 strong {
  font-size: 35px;
}
#audiometry .audiometry-left .audiometry-table .t-ear li {
  background: #f0f0f0;
  font-size: 24px;
  width: 50%;
  color: #ddd;
  text-align: center;
}
#audiometry .audiometry-left .audiometry-table .t-ear .on {
  background: #13ce66;
  color: #fff;
}
#audiometry .audiometry-left .audiometry-table #table {
  height: 430px;
  background: #fff;
}
#audiometry .audiometry-left .audiometry-table #table ul {
  float: left;
  margin-top: 30px;
  margin-left: 16px;
  text-align: right;
}
#audiometry .audiometry-left .audiometry-table #table li {
  list-style: none;
  line-height: 26px;
  font-size: 15px;
  font-weight: 600;
}
#audiometry .audiometry-left .audiometry-table #table #warp {
  height: 420px;
}
#audiometry .audiometry-left .audiometry-table #table .warp {
  margin-left: 52px;
  position: relative;
  /*频率位置*/
  /*重置按钮*/
  /*显示最大值*/
}
#audiometry .audiometry-left .audiometry-table #table .warp .trborder {
  border-bottom: 3px solid green;
}
#audiometry .audiometry-left .audiometry-table #table .warp .tdborder {
  /*border-left:  3px solid green;*/
}
#audiometry .audiometry-left .audiometry-table #table .warp .maxDbClass {
  border-left: 3px solid #13ce66;
}
#audiometry .audiometry-left .audiometry-table #table .warp th {
  height: 40px;
  font-size: 18px;
  text-align: left;
  /*text-indent: -27px;*/
}
#audiometry .audiometry-left .audiometry-table #table .warp th p {
  margin: 0px;
  margin-left: -27px;
  width: 47px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  border-radius: 6px;
  cursor: pointer;
}
#audiometry .audiometry-left .audiometry-table #table .warp th.nexthzClass p {
  background-color: #ccc;
  color: #000;
}
#audiometry .audiometry-left .audiometry-table #table .warp th.currenthzClass p {
  background-color: #13ce66;
  color: red;
}
#audiometry .audiometry-left .audiometry-table #table .warp .rest-btn span {
  display: inline-block;
  margin-left: -27px;
  letter-spacing: 2px;
  height: 40px;
  background: #da4f49;
  padding: 3px;
  color: #fafafa;
  border-radius: 3px;
  cursor: pointer;
  height: 35px;
  line-height: 35px;
  margin-top: 5px;
}
#audiometry .audiometry-left .audiometry-table #table .warp #horn {
  position: absolute;
  left: 5px;
  top: 0px;
  font-size: 20px;
  width: 26px;
  height: 26px;
  /*background: #fff;*/
  border-radius: 50%;
  line-height: 26px;
  text-align: center;
}
#audiometry .audiometry-left .audiometry-table #table .warp .horn-hidden {
  display: none;
}
#audiometry .audiometry-left .audiometry-table #table .warp .hornStopStyle {
  background-color: #fff;
  /*opacity:1;*/
  -webkit-animation: light 1000ms ease 1;
          animation: light 1000ms ease 1;
}
#audiometry .audiometry-left .audiometry-table #table .warp .hornStartStyle {
  background-color: yellow;
  color: red;
  opacity: 0.3;
  -webkit-animation-name: light;
  -webkit-animation-duration: 1000ms;
  -webkit-animation-iteration-count: infinite;
  -webkit-animation-timing-function: ease-in-out;
}
@-webkit-keyframes light {
0% {
    opacity: 0.1;
}
50% {
    opacity: 0.7;
}
100% {
    opacity: 1;
}
}
#audiometry .audiometry-left .audiometry-table #table .warp .sign,
#audiometry .audiometry-left .audiometry-table #table .warp .signMax {
  width: 15px;
  height: 15px;
  border-radius: 50%;
  position: absolute;
  left: -8px;
  top: 7px;
  background-color: blue;
}
#audiometry .audiometry-left .audiometry-table #table .warp .signMax {
  background-color: red;
}
#audiometry .audiometry-left .audiometry-table #table table {
  border-collapse: collapse;
}
#audiometry .audiometry-left .audiometry-table #table tr td {
  position: relative;
  box-sizing: border-box;
  height: 5px;
  padding: 0px;
  border: 1px solid #ccc;
}
#audiometry .audiometry-left .audiometry-table #table tr:nth-of-type(2n) td {
  border-top: 1px solid transparent;
  border-bottom: 1px solid transparent;
}
#audiometry .audiometry-left .audiometry-table #table tr:nth-of-type(2) td {
  border-top: 1px solid #ccc;
}
#audiometry .c-db {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.4);
  z-index: 3000;
}
#audiometry .c-db .c-warp {
  position: fixed;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 450px;
  box-sizing: border-box;
  padding: 10px;
  min-height: 150px ;
  background: #fafafa;
  border-radius: 5px;
  box-shadow: 0 0 20px #000;
}
#audiometry .c-db h3 {
  height: 50px;
  line-height: 50px;
  text-align: center;
  color: #333;
  font-size: 22px;
}
#audiometry .c-db .no-test-hz {
  font-size: 20px;
  color: rebeccapurple;
  text-indent: 2em;
}
.el-message-box.msgboxClass {
  width: 500px;
}
.el-message-box.msgboxClass .el-message-box__header .el-message-box__title {
  font-size: 20px;
}
.el-message-box.msgboxClass .el-message-box__close {
  display: none;
}
.el-message-box.msgboxClass .el-message-box__content {
  padding: 20px;
}
.el-message-box.msgboxClass .el-message-box__content p div {
  margin: 10px 0;
}
.el-message-box.msgboxClass .seconddiv strong {
  color: orange;
}



