
@import "../../../../static/css/testicon.css";
.reportClass {
  color: #000;
  font-size: 14px;
}
.reportClass #youImg,
.reportClass #remarksDIV {
  margin-left: 30px;
}
.reportClass #youImg {
  padding-top: 10px;
  height: 450px;
  width: 300px;
  float: left;
}
.reportClass #zuoImg {
  margin-left: 60px;
  padding-top: 10px;
  height: 450px;
  width: 300px;
  float: left;
}
.reportClass #remarksDIV {
  margin-top: 50px;
  float: left;
}
.reportClass #remarks {
  float: left;
}
.reportClass #remarks div {
  border: 1px solid #ccc;
  padding: 10px;
  height: 212px;
  width: 385px;
  line-height: 23px;
  font-size: 14px;
  word-spacing: 8px;
  letter-spacing: 1px;
}
.reportClass #tuli {
  float: left;
  margin-left: 60px;
}
.reportClass td {
  text-align: center;
}



