
@import "../../../../static/css/testicon.css";
.reportAllClass {
  color: #000;
  font-size: 14px;
}
.reportAllClass #youImg,
.reportAllClass #remarksDIV {
  margin-left: 30px;
}
.reportAllClass #youImg {
  padding-top: 10px;
  height: 450px;
  width: 300px;
  float: left;
}
.reportAllClass #zuoImg {
  margin-left: 60px;
  padding-top: 10px;
  height: 450px;
  width: 300px;
  float: left;
}
.reportAllClass #remarksDIV {
  margin-top: 50px;
  float: left;
}
.reportAllClass #remarks {
  float: left;
}
.reportAllClass #remarks div {
  border: 1px solid #ccc;
  padding: 10px;
  height: 200px;
  width: 385px;
  line-height: 23px;
  font-size: 14px;
  word-spacing: 8px;
  letter-spacing: 1px;
}
.reportAllClass #tuli {
  float: left;
  margin-left: 60px;
}
.reportAllClass td {
  text-align: center;
}
.print-tone {
  background-color: #fff;
  width: 200mm;
  height: 297mm;
  border: 1px solid #fff;
  margin: auto;
  padding: 27px;
  position: relative;
}
.print-tone .liu {
  /*表格的排列样式*/
}
.print-tone .liu .print-content {
  margin-top: 20px;
}
.print-tone .liu .tone-table {
  margin: 0px auto;
  margin-top: 20px;
  width: 100%;
}
.print-tone .liu .tone-table table {
  width: 100%;
  border-collapse: collapse;
}
.print-tone .liu .tone-table .headerbg {
  background-color: #eef1f6;
}
.print-tone .liu .tone-table .gray {
  background-color: #eee;
}
.print-tone .liu .tone-table .last-l-t {
  margin-top: 30px;
}
.print-tone .liu .tone-table .last-l-t em {
  width: 375px;
  max-width: 500px;
}
.print-tone .liu .tone-table td,
.print-tone .liu .tone-table th {
  box-sizing: border-box;
  text-align: center;
  padding: 0px;
  border: 1px solid #ccc;
  width: 50px;
}
.print-tone .liu .tone-table tr {
  height: 45px;
}
.print-tone .liu .tone-table .star {
  margin-top: 5px;
}
.print-tone .liu .tone-table .star .jianyi {
  margin-top: 5px;
}
.print-tone .liu .tone-table .star i {
  margin-left: 10px;
}
.print-tone .liu .tone-table .star em {
  width: 112px;
  /*text-align: right;*/
}
.print-tone .liu .mormTable {
  width: 45%;
  float: left;
}
.print-tone .liu .mormTable:nth-child(2n) {
  margin-left: 50px;
}
.tone {
  /*表格的排列样式*/
}
.tone .print-content {
  margin-top: 20px;
}
.tone .tone-table {
  float: left;
  margin: 0px auto;
  margin-top: 10px;
  width: 100%;
  /*height: 170px;*/
  /*表头背景色*/
  /*评价*/
}
.tone .tone-table table {
  width: 100%;
  border-collapse: collapse;
}
.tone .tone-table .headerbg {
  background-color: #eef1f6;
}
.tone .tone-table .gray {
  background-color: #eee;
}
.tone .tone-table td,
.tone .tone-table th {
  box-sizing: border-box;
  text-align: center;
  padding: 0px;
  border: 1px solid #ccc;
  width: 50px;
}
.tone .tone-table tr {
  height: 35px;
}
.tone .tone-table .star {
  margin-top: 5px;
  margin-left: 0;
}
.tone .tone-table .star .jianyi {
  margin-top: 5px;
}
.tone .tone-table .star i {
  margin-left: 10px;
}
.tone .last-l-t {
  margin-top: 10px;
}
.tone .mormTable {
  width: 45%;
  float: left;
}
.tone .mormTable:nth-child(2n) {
  margin-left: 50px;
}



