
.tone-params {
  width: 1150px;
  height: 700px;
  margin: 0px auto ;
  margin-top: 60px;
  background-color: #fff;
  border: 8px solid #5f9970;
}
.tone-params .title {
  border-bottom: 3px solid #5b9b69;
  margin: 0px 20px;
}
.tone-params .title h3 {
  font-weight: 500;
  margin: 0px;
  line-height: 50px;
  font-size: 25px;
}
.tone-params .title span {
  line-height: 50px;
  font-size: 28px;
  color: #5f9770;
}
.tone-params .title span i {
  cursor: pointer;
}
.tone-params .el-checkbox {
  width: 45px;
  text-align: center;
}
.tone-params .el-checkbox .el-checkbox__label {
  display: block;
}
.tone-params .params-main {
  padding-left: 100px;
  /*单选按钮开始*/
  /*单选按钮结束*/
  /*dd{
				.active{
					background-color: #13ce66;
					color:#fff;
				}
				img{
					width: 100px;
					height: 100px;
					border-radius: 50%;
					border: 5px solid #e6f0e5;
					box-shadow: 1px 1px 1px;
					display: block;
				}
			}*/
}
.tone-params .params-main .checkradio .el-radio-button__orig-radio:checked + .el-radio-button__inner {
  background-color: #13ce66;
  border: 1px solid #13ce66;
  box-shadow: -1px 0 0 0 #13ce66;
}
.tone-params .params-main ul {
  width: 850px;
}
.tone-params .params-main ul li {
  width: 375px;
  position: relative;
  height: 35px;
  line-height: 35px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin: 5px 30px 5px 0px;
}
.tone-params .params-main ul li .check-main {
  position: absolute;
  left: 0px;
  top: 0px;
  width: 380px;
}
.tone-params .params-main ul li .check-main span {
  border-radius: 5px 0 0 5px;
  background-color: #13ce66;
  width: 80px;
  color: #fff;
  font-size: 16px;
  font-weight: 600;
  text-indent: 40px;
  height: 35px;
}
.tone-params .params-main ul li .check-main p {
  font-size: 20px;
}
.tone-params .params-main ul li .check-main p em {
  float: left;
  margin-left: 10px;
}
.tone-params .params-main ul li .el-checkbox__label {
  visibility: hidden;
}
.tone-params .params-main dt,
.tone-params .params-main dd {
  float: left;
}
.tone-params .params-main dt {
  font-size: 18px;
  color: #3377b8;
}
.tone-params .params-main .check {
  margin-left: 115px;
  margin-top: 20px;
}



