/*综合评价*/
.estimate {
  margin: 20px 0px 0px 37px;
}
.estimate h3 {
  font-size: 20px;
  margin-top: 10px;
}
.estimate li {
  line-height: 45px;
}
.estimate li .span {
  margin-right: 20px;
}
.estimate li .star {
  margin-top: 13px;
}
.estimate li p {
  width: 300px;
  margin-left: 25px;
  background: #eee;
  border-radius: 5px;
  height: 30px;
  line-height: 30px;
  margin-top: 9px;
  padding-left: 10px;
}



