
.more-high {
  height: 94%;
  width: 100%;
  background: #fff;
}
.more-high .title {
  height: 50px;
  line-height: 50px;
  border-bottom: 1px solid #88d99f;
}
.more-high .title h3 {
  font-size: 20px;
  font-weight: 600;
  text-indent: 2em;
}
.more-high section {
  box-sizing: border-box;
  width: 100%;
  padding: 10px 50px;
  /*****定义表格样式******/
}
.more-high section .header {
  border-bottom: 3px solid #88d99f;
  /*******清空与查询按钮*****/
}
.more-high section .header .td {
  min-width: 220px;
  width: 80%;
  float: left;
  height: 100px;
  padding: 0 10px 5px 10px;
  box-sizing: border-box;
}
.more-high section .header .td > div {
  width: 100%;
  margin: 10px auto;
}
.more-high section .header li {
  margin-top: 9px;
  margin-left: 20px;
}
.more-high section .header li .el-button {
  font-size: 16px;
  padding: 13px 18px;
}
.more-high section .header .td .el-form-item {
  margin-bottom: 5px;
  margin-right: 20px;
  width: 22%;
}
.more-high section .header .td .el-form-item .el-form-item__label {
  border: 1px solid #bfcbd9;
  border-right: 0px none;
  height: 45px;
  line-height: 45px;
  padding: 0px;
  border-radius: 4px 0 0 4px;
  text-align: center;
}
.more-high section .header .td h4 {
  font-size: 18px;
  text-align: center;
  color: #20a0ff;
  font-weight: 600;
}
.more-high section .header .td .el-checkbox-group .el-checkbox {
  margin: 5px 0 0 10px;
}
.more-high section .header .product {
  text-align: center;
  font-size: 18px;
  margin: 10px 0;
}
.more-high section form {
  width: 100%;
  /******在校状态*********/
  /******查询地址*******/
  /*********助听设备************/
  /*******入园时间******/
  /******评估完成情况******/
  /*******多选状态PTA********/
}
.more-high section form label {
  color: #000;
  font-size: 16px;
}
.more-high section form .name,
.more-high section form .gender {
  width: 100%;
}
.more-high section form .zhuang {
  width: 100%;
}
.more-high section form .age-warp {
  position: relative;
}
.more-high section form .age-warp .tips {
  position: absolute;
  left: 0px;
  top: 20px;
  font-size: 12px;
  color: red;
  z-index: 100;
}
.more-high section form .age {
  width: 100%;
}
.more-high section form .el-form-item__content {
  width: 62%;
}
.more-high section form .old-age input {
  border-radius: 4px !important;
}
.more-high section form .bottom {
  width: 100%;
}
.more-high section form .el-input__inner {
  height: 45px !important;
  border-radius: 0 4px 4px 0;
}
.more-high section form .in .el-radio-button__inner {
  border-radius: 0;
}
.more-high section form .el-form-item .el-radio-button__inner {
  padding: 14.5px 15px;
}
.more-high section form .address {
  width: 100%;
}
.more-high section form .address select {
  width: 31%;
  padding: 0;
  padding-left: 5px;
  height: 30px;
  font-size: 16px;
  color: #333;
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
}
.more-high section form .el-cascader .el-input__inner {
  border-radius: 4px;
}
.more-high section form .sh-year {
  /*******隐藏日期字体图标******/
}
.more-high section form .sh-year .el-date-editor.el-input {
  width: 100%;
}
.more-high section form .sh-year i {
  display: none;
}
.more-high section form .sh-year .el-input__icon + .el-input__inner {
  padding: 3px 5px;
}
.more-high section form .end-year .el-input__inner {
  border-radius: 4px;
}
.more-high section form .isover p {
  width: 100%;
  margin-bottom: 2px;
}
.more-high section form .isover p .el-radio-group {
  width: 100%;
}
.more-high section form .isover p .el-radio-group .el-radio-button {
  width: 50%;
}
.more-high section form .isover p .el-radio-group .el-radio-button__inner {
  display: block;
  width: 100%;
  padding: 0;
  height: 35px;
  line-height: 35px;
}
.more-high section form .radio-time {
  width: 100%;
}
.more-high section form .radio-time .el-checkbox-button {
  width: 25%;
}
.more-high section form .radio-time .el-checkbox-button__inner {
  display: block;
  width: 100%;
  padding: 0;
  height: 35px;
  line-height: 35px;
}
.more-high section form .checked-more .el-select__tags > span {
  overflow: hidden;
  display: block;
  white-space: nowrap;
  width: 100%;
  height: 30px;
}
.more-high section .table {
  /*******分页********/
}
.more-high section .table .table-content {
  margin-top: 20px;
  width: 80%;
  margin-left: 40px;
  position: relative;
  /******全选按钮*******/
  /****表格头******/
  /****表格内容******/
}
.more-high section .table .table-content .check {
  display: inline-block;
  height: 50px;
  width: 50px;
  border-radius: 5px 0 0 5px;
  line-height: 50px;
  background: #fff;
  text-align: center;
  position: absolute;
  left: 1px;
  top: 1px;
  cursor: pointer;
}
.more-high section .table .table-content .table-title,
.more-high section .table .table-content ul {
  width: 100%;
  height: 40px;
  line-height: 40px;
  text-align: center;
}
.more-high section .table .table-content ul span {
  display: inline-block;
  width: 11.11%;
}
.more-high section .table .table-content .table-title p > span {
  display: inline-block;
  width: 11.11%;
}
.more-high section .table .table-content .table-title {
  width: calc(99% - 50px);
  padding-left: 50px;
  border: 1px solid #1d8ce0;
  background-color: #1d8ce0;
  border-radius: 5px;
  height: 50px;
  line-height: 50px;
  font-weight: 600;
  color: #fff;
  font-size: 18px;
  letter-spacing: 2px;
}
.more-high section .table .table-content .table-title .ch-all {
  box-sizing: border-box;
  display: inline-block;
  width: 50px;
  height: 50px;
  color: #000;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
}
.more-high section .table .table-content .table-title .em-name {
  font-weight: 600;
  margin-left: -10px;
}
.more-high section .table .table-content ul {
  height: 366px;
  overflow: auto;
  /*******active状态下选择款的样式*******/
}
.more-high section .table .table-content ul li {
  width: calc(99% - 50px);
  padding-left: 50px;
  border: 1px solid #ccc;
  margin: 10px 0;
  border-radius: 5px;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.5);
  position: relative;
  cursor: pointer;
  /******每行的选择框*******/
}
.more-high section .table .table-content ul li em {
  position: absolute;
  left: 0px;
  top: 0px;
  border-radius: 5px 0 0 5px;
  background: #fff;
  display: inline-block;
  width: 50px;
}
.more-high section .table .table-content ul li .icon-unselected {
  display: block;
}
.more-high section .table .table-content ul li .icon-xuanze3 {
  display: none;
}
.more-high section .table .table-content ul .active .icon-unselected {
  display: none;
}
.more-high section .table .table-content ul .active .icon-xuanze3 {
  display: block;
  color: #20a0ff;
}
.more-high section .table .table-content ul .active {
  background: #13ce66;
  -webkit-animation-name: morelight;
  -webkit-animation-duration: 500ms;
  -webkit-animation-iteration-count: 1;
  -webkit-animation-timing-function: ease-in-out;
  border: 1px solid #13ce66;
}
@-webkit-keyframes morelight {
0% {
    background: #fff;
}
100% {
    background: #13ce66;
}
}
.more-high section .table .btn {
  margin-left: 5%;
  margin-top: 3%;
}
.more-high section .table .btn li {
  margin-top: 10px;
}
.more-high section .table .btn li i {
  font-size: 22px;
}
.more-high section .table .btn li button {
  width: 120px;
  font-size: 16px;
}
.more-high section .table .page {
  float: right;
  margin-top: 10px;
  padding: 0;
}
.more-high section .table .page .el-pager li,
.more-high section .table .page button {
  min-width: 45px;
  height: 35px;
  line-height: 35px;
  font-size: 14px;
}
.more-high section .table .page span {
  line-height: 35px;
  font-size: 14px;
}
.more-high .print {
  /*按钮的样式外包*/
}
.more-high .print #print-tone {
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  z-index: 2000;
}
.more-high .print .print-warp {
  width: 230mm;
  height: 190mm;
  margin-left: 150px;
  background-color: #ccc;
  overflow-y: auto;
  position: fixed;
  padding: 10px 0px;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
}
.more-high .print .print-tone {
  background-color: #fff;
  width: 210mm;
  height: 297mm;
  border: 1px solid #fff;
  margin: auto;
  padding: 27px;
  position: relative;
}
.more-high .print .fiexd-w {
  width: 1000px;
  /*background: #fff;*/
  position: fixed;
  top: 0px;
  left: 0px;
  bottom: 0px;
  right: 0px;
  margin: auto;
}



