
.l-g-w {
  /*******login*********/
}
.l-g-w .el-button--primary:focus,
.l-g-w .el-button--primary:hover,
.l-g-w .el-button--primary {
  background-color: #e3771d;
  border-color: #e3771d;
  letter-spacing: 20px;
}
.l-g-w .el-form-item__content {
  position: relative;
}
.l-g-w .el-form-item__content ul {
  width: 100%;
  position: absolute;
  left: 0px;
  top: 36px;
  z-index: 100;
  background-color: #fff;
  border-radius: 5px;
  max-height: 144px;
  overflow-y: auto;
}
.l-g-w .el-form-item__content ul li {
  margin: 3px 0;
  height: 36px;
  line-height: 36px;
  position: relative;
  text-indent: 1em;
}
.l-g-w .el-form-item__content ul li i {
  position: absolute;
  top: 10px;
  right: 10px;
}
.l-g-w .el-form-item__content ul li i:hover {
  color: red;
}
.l-g-w .el-form-item__content ul li:hover {
  background-color: #20a0ff;
}
.l-g-w .el-form-item__content ul .active-li {
  color: #333;
  background-color: #20a0ff;
}
.l-g-w .l_in p {
  font-size: 26px;
  width: 80px;
  letter-spacing: 10px;
  color: #555;
}
.l-g-w .input {
  width: 310px;
  position: relative;
}
.l-g-w .el-input .el-input__inner {
  background-color: #f9ffbd ;
  background-color: #fff ;
  border-color: #a94442;
  border-color: #ccc;
  height: 40px;
  font-size: 14px;
}
.l-g-w .el-form-item {
  margin-bottom: 40px;
}
.l-g-w .el-input .el-input__inner:focus {
  background-color: yellow;
}
.l-g-w .el-autocomplete {
  width: 100%;
}
.l-g-w .el-form-item__error {
  left: 80px;
  font-size: 16px;
}
.l-g-w .my-autocomplete li {
  line-height: normal;
  padding: 7px;
  position: relative;
}
.l-g-w .my-autocomplete li .name {
  text-overflow: ellipsis;
  overflow: hidden;
}



