
.dangan {
  height: 100%;
}
.dangan .title {
  border-bottom: 1px solid #e7eaec;
}
.dangan .title h2 {
  margin-left: 20px;
  line-height: 70px;
}
.dangan .table {
  padding: 10px;
}
.dangan .table table {
  width: 100%;
}
.dangan .table .el-table__body-wrapper {
  /*****隐藏水平滚动条*****/
  overflow-x: hidden;
}
.dangan .table .el-table .cell,
.dangan .table .el-table th > div {
  padding-left: 5px;
  padding-right: 5px;
}
.dangan .dangan-main {
  padding: 10px!important;
}
.dangan .dangan-main .delete {
  float: left;
}
.dangan .dangan-main .search {
  float: right;
  width: 60%;
}
.dangan .dangan-main .el-button {
  margin-left: -2px;
  height: 36px;
  min-width: 70px !important;
  width: 70px !important;
}
.dangan .el-table_1_column_10 {
  overflow: hidden;
  /*自动隐藏文字*/
  text-overflow: ellipsis;
  /*文字隐藏后添加省略号*/
  white-space: nowrap;
  /*强制不换行*/
}
div.el-message {
  top: 50%;
  min-width: 150px;
  background-color: rgba(0, 0, 0, 0.3);
}
div.el-message p {
  color: #fff;
}



