
.class-re .cl-list p {
  height: 40px;
  line-height: 40px;
  font-size: 20px;
  background: #ccc;
  width: 200px;
  text-align: center;
  padding: 0 10px;
  border-radius: 5px;
  margin-bottom: 20px;
}
.class-re .cl-list .el-checkbox {
  margin-left: 20px;
  margin-bottom: 10px;
}
.class-re .cl-list .cl-l-check {
  max-height: 200px;
  overflow: auto;
}
.class-re .cl-list label span {
  font-size: 20px;
  line-height: 30px;
}
.class-re .cl-list label .el-checkbox__label {
  float: right;
  width: 100px;
  display: block;
  overflow: hidden;
}
.class-re .cl-list .btn {
  margin-top: 20px;
}
.class-re .cl-list .btn li {
  cursor: pointer;
  width: 120px;
  line-height: 35px;
  height: 35px;
  /*border:1px solid #ccc;*/
  border-radius: 3px;
  text-align: center;
  font-size: 18px;
  letter-spacing: 3px;
  color: #fff;
  box-shadow: 3px 3px 2px #ccc;
}
.class-re .cl-list .btn .dele {
  background-color: red;
}
.class-re .cl-list .btn .add {
  background-color: #20a0ff;
}



