
.params {
  width: 1150px;
  height: 700px;
  margin: 0px auto ;
  margin-top: 60px;
  background-color: #fff;
  border: 8px solid #5f9970;
}
.params .title {
  border-bottom: 3px solid #5b9b69;
  margin: 0px 20px;
}
.params .title h3 {
  font-weight: 500;
  margin: 0px;
  line-height: 50px;
  font-size: 25px;
}
.params .el-checkbox {
  width: 45px;
  text-align: center;
}
.params .el-checkbox .el-checkbox__label {
  display: block;
}
.params .params-main {
  padding-left: 100px;
  /*单选按钮开始*/
  /*单选按钮结束*/
}
.params .params-main dl {
  margin-top: 25px;
}
.params .params-main .checkradio {
  float: left;
  border: 1px solid  #bfcbd9;
  border-radius: 5px;
  padding-right: 10px;
  height: 35px;
  line-height: 35px;
}
.params .params-main dt,
.params .params-main dd {
  float: left;
}
.params .params-main dt {
  font-size: 18px;
  color: #3377b8;
}
.params .params-main .check {
  margin-left: 115px;
  margin-top: 20px;
}



