
#back-btn {
  overflow: hidden;
}
#back-btn .back {
  float: right;
  width: 120px;
  height: 50px;
  margin-top: 12px;
  font-weight: 600;
  cursor: pointer;
  text-align: center;
}
#back-btn .button.button-danger {
  background: #da4f49;
  color: #fafafa;
}
#back-btn .button.button-danger:hover,
#back-btn .button.button-danger:focus {
  background: #d0312a;
  color: #fafafa;
}
#back-btn .button-large {
  height: 50px;
  padding: 0 18px;
  border-radius: 6px;
  box-shadow: inset 0 -4px 0 0 rgba(0, 0, 0, 0.15);
  font-size: 22px;
  line-height: 46px;
}
#back-btn .button-large:active {
  line-height: 50px;
}



