
.login-container {
  background-clip: padding-box;
  padding: 35px 35px 15px 35px;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.login-container .title {
  margin: 0px auto 40px auto;
  text-align: center;
  color: #505458;
}
.login-container .remember {
  margin: 0px 0px 35px 0px;
}
.login {
  background-color: #1c84c6;
  background: url(../../../static/images/bg.jpg) no-repeat 0 0;
  background-size: 100% 100%;
  height: 100%;
  position: relative;
  /*******login*********/
  /****重启板块样式*******/
}
.login .t_logo {
  width: 530px;
  height: 60px;
  position: absolute;
  left: 7%;
  top: 8%;
  background: url(../../../static/images/login-logo.png) no-repeat 0 0;
}
.login .b_img {
  width: 115px;
  height: 49px;
  margin-left: 275px;
  margin-bottom: 10px;
  background: url(../../../static/images/bg2.png) no-repeat 0 0;
}
.login .g_btn ul {
  position: relative;
}
.login .g_btn li {
  position: relative;
  width: 65px;
  height: 70px;
  background-color: #1ab297;
  border-radius: 5px;
  cursor: pointer;
  text-align: center;
  box-shadow: inset -1px -5px 4px -1px;
}
.login .g_btn li i {
  font-size: 28px;
  color: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  margin: 0;
  display: block;
}
.login .g_btn li:hover i {
  color: #1c84c6;
}
.login .g_btn .z_z {
  position: absolute;
  top: 0;
  left: 40%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
}
.login .g_btn .c_q {
  position: absolute;
  top: 0;
  right: 0%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  background-color: #ee5567;
}
.login .g_btn .g_b {
  background-color: #f7ad58;
}
.login .g_btn p {
  margin-top: 80px;
  text-align: center;
  font-size: 16px;
  font-weight: 600;
  color: #666;
}
.login .title {
  color: #fff;
  margin: 10px;
}



