/****内容顶部样式****/
.main-title {
  /*****返回主页按钮******/
}
.main-title .main-t-l {
  width: 305px;
  height: 50px;
  position: relative;
}
.main-title .main-t-l h2 {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 100;
  color: #fff;
}
.main-title .main-t-l div {
  width: 196px;
  position: absolute;
  height: 0px;
  left: 0px;
  top: 0px;
  border-left: 50px solid transparent;
  border-bottom: 50px solid #2e9872;
  border-right: 50px solid transparent;
}
.main-title .main-t-r {
  /*margin-right: 24px;*/
}
.main-title .main-t-r a {
  text-decoration: none;
}
.main-title .main-t-r .button {
  display: block;
  text-align: center;
  cursor: pointer;
  border: none;
  transition-property: background-color;
  transition-duration: 0.2s;
  transition-timing-function: linear;
  background: #2e9872;
  color: #222;
}
.main-title .main-t-r .button-large {
  height: 45px;
  padding: 0 18px;
  border-radius: 6px;
  box-shadow: inset 0 -4px 0 0 rgba(0, 0, 0, 0.15);
  font-size: 22px;
  line-height: 40px;
}
.main-title .main-t-r .button-large i {
  line-height: 40px;
}
.main-title .main-t-r .button-large:active,
.main-title .main-t-r .button-large:active i {
  line-height: 50px;
}
.main-title .main-t-r .button.button-warning {
  background: #2e9872;
  color: #fafafa;
}
.main-title .main-t-r .button.button-warning:hover,
.main-title .main-t-r .button.button-warning:focus {
  background: #f9960a;
  color: #fafafa;
}
.main-title .main-t-r .button-large.button-warning {
  box-shadow: inset 0 -4px 0 0 rgba(0, 0, 0, 0.2);
}



